import styled from 'styled-components';

export const Card = styled.div`
    width: 100%;
    position: relative;
    margin-bottom: 1rem;
    padding: 0 0 3.5rem;
    padding-right: 1.6rem;

    ${({ itemCategory }) => itemCategory.includes('Drinks') ? `
    -ms-grid-columns: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    display: grid;
    color: #000000;
    ` : `
    display: flex;
    flex-direction: column;
    color: #ffffff;
    `}

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 10px;
        overflow: hidden;
        z-index: 0;
        -webkit-clip-path: inset(2rem 0 2rem 2rem round 10px);
        box-shadow: rgba(0, 0, 0, 0.1) 0 0 16px;
        clip-path: inset(2rem 0 2rem 2rem round 10px);
        contain: strict;
        -webkit-transition: -webkit-clip-path 0.3s ease 0.1s;
        transition: -webkit-clip-path 0.3s ease 0.1s;
        transition: clip-path 0.3s ease 0.1s;
        transition: clip-path 0.3s ease 0.1s, -webkit-clip-path 0.3s ease 0.1s;
    }

    &::after {
        background: -moz-linear-gradient(45deg, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0) 100%);
        background: -webkit-linear-gradient(45deg, rgba(0,0,0,0.7) 0%,rgba(0,0,0,0) 100%);
        background: linear-gradient(45deg, rgba(0,0,0,0.7) 0%,rgba(0,0,0,0) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#b3000000', endColorstr='#00000000',GradientType=1 );
    }
`

export const Image = styled.div`
    z-index: 2;

    ${({ itemCategory }) => itemCategory.includes('Drinks') ? `
    
    ` : `
    position: relative;
    `}
`

export const PostImg = styled.div`
    overflow: hidden;
    position: relative;
    width: -webkit-fill-available;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
    display: flex;
    max-width: 580px;
    max-height: 230px;
    background-size: cover;
    background-position: center;

    & img {
        width: 100%;
        max-height: 268px;
        float: left;
    }
`

export const PostBttm = styled.div`
    width: calc(100% - 1.6rem);
    position: absolute;
    bottom: -0.8rem;
    display: flex;
    justify-content: flex-end;
    height: 34px;

    & > div {
        width: 43%;
        position: absolute;
        top: 0;
    }

    ${({ itemCategory }) => itemCategory.includes('Drinks') ? `
    bottom: 4rem;
    ` : `
    
    `}
`

export const Description = styled.div`
    position: absolute;
    background: rgba(0, 0, 0, 0.65);
    z-index: 0;
    border-radius: 6px;
    width: 100%;
    height: 100%;
    transition: .5s ease;
    padding: 1.6rem;
    font-size: 2rem;
    opacity: ${({ status }) => status ? 1 : 0};
    
    ${({ status }) => status ? `
    overflow-x: hidden;
    overflow-y: overlay;
    ` : `
    
    `}
`

export const BttnsAndInput = styled.div`
    display: grid;
    grid-template-columns: minmax(32px,0fr) 1fr minmax(32px,0fr);
    grid-auto-rows: 1fr;
    box-shadow: -4px 4px 0 0 rgba(0, 0, 0, 0.2);
    border-radius: 3px;
    background: #2bd660;

    & > * {
        height: 34px;
    }

    & > input {
        color: #fff;
        background: rgba(4, 4, 4, 0.18);
        text-align: center;
        font-weight: 500;
    }
`

export const Fieldset = styled.div`

    & button {
        width: 100%;
        background: transparent;
        justify-content: center;
        height: 100%;
        display: flex;
        align-items: center;
    }
`

export const ButtonsCrement = styled.div`
    display: ${({ status }) => status ? 'block' : 'none'};
`

export const AddItem = styled.div`
    display: ${({ status }) => status ? 'none' : 'block'};

    & span {
        color: #fff;
    }

    & button {
        color: #fff;
        background: #2bd660;
        box-shadow: 0 0 2px 0 #2bd660, -4px 4px 0 0 rgba(0, 0, 0, 0.2);
        width: 100%;
        padding: 1rem;
        text-transform: uppercase;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        border-radius: 3px;
        font-weight: 500;
    }
`

export const Info = styled.div`
    z-index: 1;

    & > span {
        min-width: 92px;
    }

    ${({ itemCategory }) => itemCategory.includes('Drinks') ? `
    margin-left: 1.6rem;
    padding-right: 1.6rem;
    position: absolute;
    width: 47.5%;
    right: 0;
    top: 32px;

    & > span {
        font-size: 2.8rem;
        font-weight: 400;
    }

    & > div {
        
        & > h3 {
            margin-bottom: 1.2rem;
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
            font-size: 2rem;
        }

        & > span {
            font-weight: 500;
        }
    }
    
    ` : `
    display: flex;
    padding: 1.6rem 0 0;

    & > *:not(:last-child) {
        margin-right: 1.6rem;
    }

    & > div {
        width: 100%;
        display: inline-grid;

        & > h3 {
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
        }
    }

    & > span {
        font-size: 2.5rem;
        background: #131313;
        box-shadow: 4px 4px 0 0 rgba(0, 0, 0, 0.2);
        font-family: 'Oswald',arial,helvetica,sans-serif;
        font-weight: 300;
        border-radius: 6px;
        text-align: center;
        padding: 0.9rem 0;
    }
    `}
`

export const Unavailable = styled.div`
    color: #ffffff;
    display: flex;
    box-shadow: -4px 4px 0 0 rgba(0,0,0,0.2);
    border-radius: 3px;
    background: #d62b2b;
    align-items: center;
    justify-content: center;
    height: 34px;
`