import styled from 'styled-components';

export const Nav = styled.nav`
    width: 100vw;
    background: #fff;
    border-top-color: rgba(0, 0, 0, 0.15);
    box-shadow: 1px 0 0 1px rgba(0, 0, 0, 0.03);
    border-top-style: solid;
    border-top-width: 1px;
    padding: 0 2rem;
    position: fixed;
    display: flex;
    height: 48px;
    bottom: 0;
    z-index: 6;

    & li:not(:last-child) {
        margin-right: 1.6rem;
    }

    & li {
        width: 100%;
        align-items: center;
        justify-content: center;
        display: flex;

        & button {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 38px;
            height: 38px;
        }
    }
`

export const Fire = styled.div`
    position: absolute;
    width: 32px;
    height: 32px;
    background-color: transparent;
    margin-left: auto;
    margin-right: auto;
    z-index: 1;
`

export const Fireleft = styled.div`
    position: absolute;
    height: 100%;
    width: 100%;
    animation: shake 3s ease-out 0;
    animation-iteration-count: infinite;
    animation-fill-mode: both;

    & > div:nth-child(1) {
        position: absolute;
        top: 12%;
        left: -17%;
        width: 80%;
        height: 80%;
        background-color: #ef5a00;
        transform: scaleX(0.8) rotate(45deg);
        border-radius: 0 40% 60% 40%;
        filter: drop-shadow(0 0 10px #d43322);
    }

    & > div:nth-child(2) {
        position: absolute;
        top: 10%;
        left: 20%;
        width: 10%;
        height: 10%;
        background-color: #ef5a00;
        border-radius: 50%;
        filter: drop-shadow(0 0 10px #d43322);
        animation: particleUp 3s infinite ease-out 0;
        animation-fill-mode: both;
    }
`

export const Firemain = styled.div`
    position: absolute;
    height: 100%;
    width: 100%;
    animation: scaleUpDown 3s ease-out;
    animation-iteration-count: infinite;
    animation-fill-mode: both;
    z-index: 1;
    opacity: .9;

    & > div:nth-child(1) {
        position: absolute;
        top: 5%;
        width: 100%;
        height: 100%;
        background-image: radial-gradient(farthest-corner at 10px 0, #d43300 0%, #ef5a00 70%);
        transform: scaleX(0.8) rotate(45deg);
        border-radius: 0 40% 60% 40%;
        filter: drop-shadow(0 0 10px #d43322);
    }

    & > div:nth-child(2) {
        position: absolute;
        top: 60%;
        left: 45%;
        width: 10px;
        height: 10px;
        background-color: #ef5a00;
        border-radius: 50%;
        filter: drop-shadow(0 0 10px #d43322);
        animation: particleUp 1.7s ease-out 0;
        animation-iteration-count: infinite;
        animation-fill-mode: both;
    }
`

export const Fireright = styled.div`
    height: 100%;
    width: 100%;
    position: absolute;
    animation: shake 2s ease-out 0;
    animation-iteration-count: infinite;
    animation-fill-mode: both;

    & > div:nth-child(1) {
        position: absolute;
        top: 12%;
        right: -17%;
        width: 80%;
        height: 80%;
        background-color: #ef5a00;
        transform: scaleX(0.8) rotate(45deg);
        border-radius: 0 40% 60% 40%;
        filter: drop-shadow(0 0 10px #d43322);
    }

    & > div:nth-child(2) {
        position: absolute;
        top: 45%;
        left: 50%;
        width: 15px;
        height: 15px;
        background-color: #ef5a00;
        transform: scaleX(0.8) rotate(45deg);
        border-radius: 50%;
        filter: drop-shadow(0 0 10px #d43322);
        animation: particleUp 2s ease-out 0;
        animation-iteration-count: infinite;
        animation-fill-mode: both;
    }
`

export const Firebttm = styled.div`

    & > div:nth-child(1) {
        position: absolute;
        top: 30%;
        left: 20%;
        width: 75%;
        height: 75%;
        background-color: #ff7800;
        transform: scaleX(0.8) rotate(45deg);
        border-radius: 0 40% 100% 40%;
        filter: blur(10px);
        animation: glow 2s ease-out 0;
        animation-iteration-count: infinite;
        animation-fill-mode: both;
    }

`

export const Button = styled.button`
    position: relative;

    ${({ active }) => active && `
        & svg {
            fill: #000000;
        }
    `}
`

export const Icon = styled.span`
    background: #c00;
    color: #fff;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    text-align: center;
    position: absolute;
    font-size: 1.3rem;
    line-height: 2.2rem;
    font-style: normal;
    z-index: 1;
    top: 0;
    right: 0;
`