import React from 'react';
import Bracael from '../Bracael';
import ComponentExtraPedido from './ComponentExtraPedido';

function ComponentPedidoDetail({ detail }) {

    return detail.map((item, i) => {

        return (
            <React.Fragment key={i}>
                <li>
                    <span>{new Array(3 + 1 - (i + '').length).join('0') + ++i}</span>
                    <span>{item.id}</span>
                    <span>{item.title} {item.weight ? Bracael.unitSize({ ml: item.weight }) : item.grams ? Bracael.unitSize({ gm: item.grams }) : ''}</span>
                    <span>{item.amount}un</span>
                    <span>{Number(item.price * item.amount).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
                </li>
                {item.extra && <ComponentExtraPedido { ...item} />}
            </React.Fragment>
        )
    })
}

export default ComponentPedidoDetail;