import styled from 'styled-components';

export const CarouselHome = styled.section`

    ${({ dataLength }) => {
            // console.log(dataLength)
        }
    }

    & > button {
        width: 100%;
        font-size: 2.4rem;
        font-weight: 100;
        padding: 1.6rem 1rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
`

export const CardSmall = styled.div`
    color: hsl(0, 0%, 60%);
    box-shadow: hsl(0, 0%, 80%) 0 0 16px;
    overflow: hidden;
    background-color: hsl(0, 0%, 100%);
    width: calc(74vw);
    max-width: 334px;
    border-radius: 6px;
    padding: 1.6rem;
    display: flex;
`

export const Thumb = styled.div`
    min-width: 92px;
    max-width: 92px;
    height: 92px;
    overflow: hidden;
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
    background-size: cover;
    background-position: center center;
    background-color: rgb(232, 232, 232);
    border-radius: 6px;
    margin-right: 1.6rem;

    & img {
        display: block;
    }
`

export const SlideCarousel = styled.div`
    display: flex;
`

export const Info = styled.div`
    width: 100%;
    flex-direction: column;
    display: flex;

    & span:not(:last-child) {
        margin-bottom: 1rem;
    }
`

export const InfoTitle = styled.span`
    font-weight: 700;
    color: #2a2a2a;
`

export const InfoPrice = styled.span`
    display: flex;
    flex-direction: column;
`