import React from 'react';
import { SnapshotConnection } from '../ContextConnection';

function ComponentItensAttendance() {

    // eslint-disable-next-line no-unused-vars
    const { GoConnection, EndConnection } = SnapshotConnection();
    const { order } = GoConnection;

    return Object.entries(order).map(e => (
        <div key={e[0]}>
            <span>{e[1].client}</span>
            <div><p>Status</p>{e[1].status === 'Em espera' ? <p>{e[1].status}</p> : e[1].status === 'Em preparo' ? <p>{e[1].status}</p> : e[1].status === 'Em retirada' ? <p>{e[1].status}</p> : <p style={{ color: "#0fac42" }}>{e[1].status}</p>}</div>
        </div>
      ))
}

export default ComponentItensAttendance;