import styled from 'styled-components';
import { BorderRadius, BoxSizing } from './root';

export const Header = styled.header`

    & > div {
        margin: 0 auto;
        position: relative;

        @media (min-width: 980px) {
            max-width: 1096px;
            padding: 0 1rem;
        }
        
        & > div:nth-child(3)  {
            padding: 0 1.6rem 0;
            overflow: hidden;
        }
    }
`

export const ChildHeader = styled.div`
    width: 100%;
    max-width: 1170px;
    margin: 0 auto;
    align-items: center;
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 0fr minmax(72%,0fr) 0fr;
    grid-template-columns: 0fr 1fr 0fr;
    grid-template-rows: 1fr;
    padding: 2.2rem 1.6rem 0;

    & button {
        position: relative;
        padding: 7px;
        width: 18px;
        height: 20px;
        ${BoxSizing('initial')}

    }
`

export const ChildStatus = styled.div`
    padding: 2rem 1.6rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    & h1 {
        font-size: 3rem;
        font-weight: 500;
    }
`

export const ChildMenu = styled.div`
    display: flex;
    padding: 0 3rem 1rem;
    margin-bottom: 1rem;
    overflow: hidden;
`

export const Marca = styled.section`
    background-size: 110px;
    background-repeat: no-repeat;
    background-position: center;
    height: 28px;

    & {
        background-image: url("data:image/svg+xml;base64,");
    }
`

export const MenuButton = styled.button`
    width: 18px;
    height: 18px;
    display: block;
    ${BorderRadius('initial')}
    ${BoxSizing('50%')}
    position: relative;
`

export const Menu = styled.i`
    top: 16px;
    left: 7px;

    &, &::before, &::after {
        position: absolute;
        width: 18px;
        height: 2px;
        -webkit-transition: transform .3s linear, top .3s linear, background .25s linear;
        -o-transition: transform .3s linear, top .3s linear, background .25s linear;
        transition: transform .3s linear, top .3s linear, background .25s linear;
        background: #777777;
    }

    &::before {
        top: 5px;
    }

    &::after {
        top: -5px;
    }

    &::before, &::after {
        content: '';
        left: 0;
    }

    ${({ active }) => active === 'Pushmenu' ? `
    background: none !IMPORTANT;

    &::before, &::after {
        top: 0;
    }

    &::before {
        -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        transform: rotate(-45deg);
    }

    &::after {
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
    }

    ` : `

    `}
`

export const Item = styled.li`
    text-align: center;

    & button {
        color: rgba(0, 0, 0, 0.6);
        font-weight: 500;
        font-size: 16px;
        position: relative;
        padding: 0 0 1rem;
        display: inline-block;
        transition: all 0.5s;

        &::after {
            content: '';
            display: block;
            width: 40%;
            height: 3px;
            left: 30%;
            right: 30%;
            transition: 0.2s;
            background-color: rgba(0, 0, 0, 0.4);
            border-radius: 3px;
            position: absolute;
            bottom: 3px;
        }
    }
`

export const Button = styled.button`
    ${({ active }) => active && `
        color: rgba(0,0,0,1) !IMPORTANT;

        &::after {
            width: 90% !IMPORTANT;
            background-color: rgba(0,0,0,1) !IMPORTANT;
            left: 5% !IMPORTANT;
            right: 5% !IMPORTANT;
        }
    `}
`