import styled from 'styled-components';
import { BorderRadius, Even } from './root';

export const Container = styled.div`
    background: #ffffff;
    border-radius: 6px;
    box-shadow: rgba(0,0,0,0.1) 0 0 16px;
    font-weight: 100;
    font-size: 1.5rem;
    overflow: overlay;
    margin-bottom: 1.6rem;

    & > small {
        width: 80%;
        display: flex;
        height: 30vh;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin: 0 auto;
    }
`

export const Space = styled.div`
    width: 300%;
    position: relative;
    transition: .3s;
    overflow: hidden;

    left: ${({ stage }) => {
        if(stage === 2){
            return '-100%';
        }
        else if(stage === 3){
            return '-200%';
        }
        else {
            //igual a 1 ou primeria tela
            return '0%';
        }
    }};

    & > div {
        padding: 1.6rem;
        width: 33.333333333%;
        float: left;

        & > div:not(:last-child) {
            margin-bottom: 1rem;
        }
    }
`

export const Order = styled.div`
    padding: 1.6rem 1.6rem 0;
    display: block;

    & em {
        width: 100%;
        text-indent: 15%;
        font-weight: bold;
        display: block;
    }

    & p {
        margin: 1rem 0 0rem;
        padding: 0.8rem 1.4rem;
        background: rgba(0, 0, 0, 0.08);
        border-radius: 3px;
        font-size: 1.3rem;
    }

    & button {
        color: #fff;
        font-weight: 500;
        text-align: center;
        border-radius: 3px;
        font-size: 1.4rem;
        padding: 7px 1.6rem;
        transition: background-color 5s;
        background-color: #39ce67;
        box-shadow: inset 0 -2px 0 rgba(0, 0, 0, 0.25), 3px 3px 0 rgba(0, 0, 0, 0.15);
        margin: -20px 7% 0;
    }
`

export const Bottom = styled.div`
    display: flex;
    justify-content: space-between;

    & > button:first-child {
        background: transparent;
        font-size: 1.3rem;
        transition: color .5s;
        font-weight: 400;
        position: relative;
        text-align: left;
    }

    & > button:last-child {
        color: #fff;
        font-weight: 500;
        text-align: center;
        border-radius: 3px;
        font-size: 1.4rem;
        padding: 7px 1.6rem;
        transition: background-color .5s;
        background-color: #d2b228;
        box-shadow: inset 0 -2px 0 rgba(0, 0, 0, 0.25);
    }
`

export const Group = styled.div`
    width: 100%;
    display: inline-flex;

    & input, & input::placeholder {
        transition: .5s;
    }

    ${({ numbInput = false }) => numbInput ? `
    & input:nth-child(2) {
        max-width: 30%;
    }
    ` : ``}
    
    & input {
        width: 100%;
        background: rgba(0, 0, 0, 0.1);
        font-family: var(--font-poppins);
        text-indent: 1.8rem;
        border-radius: 3px;
        padding: 1.5rem 0;
        display: block;
        font-weight: 400;
        font-size: 1.5rem;

        &:not(:last-child) {
            margin-right: 1rem;
        }

        ${({ typeColumn = false }) => typeColumn ? `
        &:nth-child(2) {
            max-width: 30%;
        }
        ` : `` }
    }
`

export const Alert = styled.div`
    border-left: 4px solid #e4bd16;
    background: rgba(0, 0, 0, 0.15);
    position: relative;
    border-radius: 4px;
    display: flex;
    justify-content: space-between;
    padding: 1rem 1.6rem;

    & svg {
        position: relative;
        width: 16px;
        height: 16px;
        top: .45rem;
    }

    & p {
        width: 100%;
        margin-left: 1.6rem;
        display: block;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        font-weight: 100;
    }
`

export const Select = styled.div`
    background: rgba(0, 0, 0, 0.1);
    border-radius: 3px;
    height: 200px;
    display: flex;
    padding-top: 1rem;
    padding-right: 1rem;
    padding-bottom: 1rem;
    position: relative;
    overflow: hidden;
    padding-left: 0;

    &::before {
        top: 1rem;
        background: #e5e5e5;
        background: -moz-linear-gradient(bottom, #e5e5e5 0%, transparent 100%);
        background: -webkit-linear-gradient(bottom, #e5e5e5 0%,transparent 100%);
        background: linear-gradient(to bottom, #e5e5e5 0%,transparent 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e5e5e5', endColorstr='#ffffff00',GradientType=0 );
    }

    &::after {
        bottom: 1rem;
        background: #e5e5e5;
        background: -moz-linear-gradient(top, #e5e5e5 0%, transparent 100%);
        background: -webkit-linear-gradient(top, #e5e5e5 0%,transparent 100%);
        background: linear-gradient(to top, #e5e5e5 0%,transparent 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e5e5e5', endColorstr='#ffffff00',GradientType=0 );
    }

    &::before, &::after {
        content: "";
        width: 100%;
        height: 20px;
        display: block;
        position: absolute;
        z-index: 5;
        left: 0;
    }

    & > div {
        overflow-x: hidden;
        overflow-y: overlay;
        padding-right: 6px;
        width: 100%;

        ${({ listLength }) => listLength ? '' : `
        &::after {
            content:"No h resultados";
        }

        & {
            display: flex;
            align-items: center;
            justify-content: center;
        }
        `};

        & > div {
            display: flex;
            align-items: center;
            padding-top: 1.2rem;
            padding-right: 1.4rem;
            padding-bottom: 1.2rem;
            padding-left: 1.8rem;
            position: relative;

            &:nth-child(even) {
               ${Even}
            }

            & span:nth-child(1) {
                margin-right: 1rem;
                font-weight: 500;
                width: 12px;
            }

            & span:nth-child(3) {
                color: #2b9b4d;
                margin-right: 1.6rem;
                font-weight: 700;
            }

            & p {
                width: 100%;
            }

            & input[type="radio"] ~ div:before {
                content: '';
                background: #f4f4f4;
                border-radius: 50%;
                border: 1px solid #b4b4b4;
                display: inline-block;
                width: 18px;
                height: 18px;
                position: relative;
                vertical-align: top;
                cursor: pointer;
                text-align: center;
                -webkit-transition: all 250ms ease;
                transition: all 250ms ease;
            }

            & input[type="radio"]:checked ~ div:before {
                background-color: #39ce67;
                box-shadow: inset 0 0 0 3px #f4f4f4;
            }

            & input[type="radio"]:focus ~ div:before {
                outline: none;
                border-color: #2b9b4d;
            }

            & input[type="radio"]:disabled ~ div:before {
                box-shadow: inset 0 0 0 4px #f4f4f4;
                border-color: #b4b4b4;
                background: #b4b4b4;
            }

            & label {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                z-index: 8;
                cursor: pointer;
            }
        }
    }
`

export const Edit = styled.div`
    display: none;

    & > div {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 1rem 1.6rem;
        background: #efefef;
        margin: 8px 0;
        
        ${BorderRadius(3)}
    
        & textarea {
            width: 100%;
            background: transparent;
            display: inline-grid;
            resize: none;
            margin-right: 1.6rem;
            height: 18px;
            line-height: 18px;
            color: #36a07e;
            font-weight: 500;
            font-size: 1.43rem;
        }

        & span {
            color: #36a07e;
        }
    
        & button {
            font-weight: 500;
        }
    }
`

export const Padding = styled.div`
    padding: 1.6rem;
`