import React from 'react';
import Bracael from '../Bracael';
import { SnapshotConnection } from '../ContextConnection';

// import { Semana } from '../styles/Semana';

function ComponentSemana() {

    // eslint-disable-next-line no-unused-vars
    const { GoConnection, EndConnection } = SnapshotConnection()
    const { business: { action } } = GoConnection;

    return action.map(function(x, i){
        return action[i].map(function(item, ii){
            if(item[0] === '' || item[1] === ''){
                return <li key={i}><span>{Bracael.u.b[i]}</span><span style={{color: "#ff0000"}}>Fechado</span></li>
            }
            else {
                if(ii !== 0){
                    return <li key={i}><span>--</span><span>{item[0]} s {item[1]}</span></li>
                }
                else {
                    return <li key={`${i}__${ii}`}><span>{Bracael.u.b[i]}</span><span>{item[0]} s {item[1]}</span></li>
                }
            }
        });
    });
}

export default ComponentSemana;