import React, { useEffect, useState } from 'react';

import { SnapshotPedido } from '../ContextTypecard';

function ComponentModalMethod({ item, data }) {

    // item[0] string (card)
    // item[1] bolean (status)
    
    const { usePedido1, setPedido1 } = SnapshotPedido()

    const Input = () => console.log('Input is declared but its value is never read.')

    const [useValue, setValue] = useState(usePedido1.payment[1][data].includes(item[0]))
    // eslint-disable-next-line no-unused-vars
    const [useFree, setFree] = useState(false)

    useEffect(() => {
        const assoc = { ...usePedido1}
        const Index = assoc.payment[1][data].indexOf(item[0])
        if(useFree) {
            if(Index !== -1) { //*existente
                assoc.payment[1][data].splice(Index, 1)
                setPedido1(assoc)
                setValue(false)
            }
            else { //* no existe
                assoc.payment[1][data].push(item[0])
                setPedido1(assoc)
                setValue(true)
            }
            setFree(false)
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useFree])


    const strMethod = {
        "banrisul": {
            name: "Banrisul"
        },
        "visa": {
            name: "Visa"
        },
        "maestro": {
            name: "Maestro"
        },
        "elo": {
            name: "Elo"
        },
        "mastercard": {
            name: "Mastercard"
        },
        "hipercard": {
            name: "Hipercard"
        },
        "amex": {
            name: "American Express"
        },
        "diners": {
            name: "Diners Club"
        },
        "alelo": {
            name: "Alelo"
        },
        "sodexo": {
            name: "Sodexo"
        },
        "ticket": {
            name: "Ticket"
        },
        "vrref": {
            name: "Vale Refeio"
        }
    }

    return Object.keys(strMethod).includes(item[0]) && item[1] ? (
        <div>
            <input type="checkbox" onChange={() => Input()} checked={useValue} />
            <label onClick={() => setFree(true)}>{strMethod[item[0]].name}</label>
        </div>
    ) : null;
    
}

export default ComponentModalMethod;