import React, { useEffect, useState } from 'react';
import Bracael from '../Bracael';
import { SnapshotWindow, SnapshotAssoc, SnapshotBusiness, SnapshotSucess } from '../ContextComanda';
import { database } from '../utils/BrowserConnection'

import ComponentCardEdit from './ComponentCardEdit';
import ComponentCashEdit from './ComponentCashEdit';
import ComponentPixEdit from './ComponentPixEdit';

import { Comanda, Message, ButtonGreen, ButtonText, Payment, Strike, Textarea, H3, Span, Info, Method, Checkradio } from '../styles/Modal';

function ComponentModalSecond({ useContent, useSecond, setShake }) {

    // eslint-disable-next-line no-unused-vars
    const { info, methods } = SnapshotBusiness()
    const { usePedido1, setPedido1 } = SnapshotAssoc()

    const { client, region, tel, comment, trade, payment: [{ cash, pix },{ debit, credit, voucher }], street, district, number, subvalue, delivery, complement, detail } = usePedido1;

    // eslint-disable-next-line no-unused-vars
    const { useWindow, setWindow } = SnapshotWindow()

    // eslint-disable-next-line no-unused-vars
    const { useSucess, setSucess } = SnapshotSucess()

    const [useCash, setCash] = useState(cash || trade)
    const [useCard, setCard] = useState(debit.length || credit.length || voucher.length)
    // eslint-disable-next-line no-unused-vars
    const [usePix, setPix] = useState(pix) //#ARRUMAR
    const [useTextarea, setTextarea] = useState(comment)
    const [useTrade, setTrade] = useState(trade) // ts: number # valor do dinheiro para troco

    useEffect(() => {
        if(!useCard) {
            const assoc = { ...usePedido1}
            assoc.payment[1] = {
                debit: [],
                credit: [],
                voucher: []
            }
            setPedido1(assoc)
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useCard])

    const Input = () => console.log('Input is assigned a value but never used.')

    const inputTextarea = event => {
        const { target } = event;
        setTextarea(target.value)
    }

    useEffect(() => {
        const assoc = { ...usePedido1}
        assoc.comment = useTextarea;
        setPedido1(assoc)
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useTextarea])

    useEffect(() => {
        if(useCash) {
            const assoc = { ...usePedido1}
            assoc.payment[0].cash = true;
            setPedido1(assoc)
        }
        else {
            const assoc = { ...usePedido1}
            assoc.payment[0].cash = false;
            assoc.trade = 0;
            setPedido1(assoc)
        }

    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useCash])

    useEffect(() => {
        if(usePix) {
            const assoc = { ...usePedido1}
            assoc.payment[0].pix = true;
            setPedido1(assoc)
        }
        else {
            const assoc = { ...usePedido1}
            assoc.payment[0].pix = false;
            assoc.trade = 0;
            setPedido1(assoc)
        }

    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [usePix])

    const sendPedido = () => {
        
        if(cash || pix || debit.length || credit.length || voucher.length) {
        
            const newPedido = {
                client,
                comment,
                detail: detail.map(data => {
                    // eslint-disable-next-line no-unused-vars
                    const { amount, comment, extra, id, price, title } = data;
                    const assoc = { amount, comment, id, price, title }
        
                    const insertExtra = () => {
                        const isValid = extra[1].map(val => val[0]).every(data => data === 0);
                        const startInsert = () => {
    
                            const newExtra = []
                            // eslint-disable-next-line array-callback-return
                            extra[1].map((item, i) => {
                        
                                item[0] && newExtra.push({
                                    item: extra[0][i][0],
                                    amount: item[0],
                                    price: item[1]
                                })
                            });
    
                            assoc.extra = newExtra;
                        }
    
                        !isValid && startInsert()
                    }
        
                    extra && insertExtra()
        
                    data.weight ?
                        assoc.weight = data.weight :
                        assoc.grams = data.grams;
        
                    return assoc;
                }),
                region,
                district,
                number,
                payment: usePedido1.payment,
                street,
                complement,
                tel, // numero de telefone
                time: new Date().toISOString(), // data do pedido
                subvalue, // subtotal do pedido
                delivery,
                trade
            };

            database.ref('request').push(newPedido)
            .then(() => {
                const newPayment = []
                cash && newPayment.push('dinheiro')
                pix && newPayment.push('pix')
                debit.length && newPayment.push('dbito')
                credit.length && newPayment.push('crdito')
                voucher.length && newPayment.push('voucher')
    
                const secondPayment = [ ...newPayment]
    
                const temp = '```'
                const comandCart = detail.map(assoc => {
                    // eslint-disable-next-line no-unused-vars
                    const { amount, title, weight, grams, extra, comment } = assoc;
    
                    const lineComment = comment ? `\n *${comment}*` : '';
                    const lineExtra = extra ? extra[0].map((val, i) => extra[1][i][0] ? `\n _${extra[1][i][0]} extra de ${val[0]}_` : '').join('') : '';
    
                    return `*${amount}x* ${temp}${title}${weight ? Bracael.unitSize({ ml: weight, space: true }) : grams ? Bracael.unitSize({ gm: grams, space: true }) : ''}${temp}${lineExtra}${lineComment}`
                })
    
                //# mantenha a template string colada na esquerda e nenhuma quebra de linha no inicio e final da string para organizao do texto a ser enviado
    
                window.open(`https://api.whatsapp.com/send?phone=55${info.whatsapp.replace(/[^0-9]/g, '')}&text=`+window.encodeURIComponent(`*# DADOS PESSOAIS*
 _Nome:_ ${temp}${client}${temp}
 _Telefone:_ ${temp}${tel}${temp}
 _Endereo:_ ${temp}${street}, ${number} - ${region}${district ? ` - ${district}` : ''}${temp}

*# SOBRE O PEDIDO*
${comandCart.join('\n')}

*# SOBRE O PAGAMENTO*${comment ? `\n _Comentrio:_ ${comment}` : ''}
${` _Forma de pagamento:_ ${newPayment.length === 1 ? newPayment.map(val => val.charAt(0).toUpperCase() + val.slice(1)).join('') : `${newPayment.map((val, i) => i <= 1 ? val.charAt(0).toUpperCase() + val.slice(1) : val).splice(newPayment.lenght-1, 1)} e ${secondPayment[secondPayment.length-1]}`}`}
${trade ? ` _Troco para:_ ${trade.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}\n` : ''}${subvalue ? ` _Subtotal:_ ${subvalue.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}\n` : ''}${delivery ? ` _Taxa de entrega:_ ${delivery.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}\n` : ''} *_TOTAL:_* ${temp}${Number(subvalue + delivery).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}${temp}`));

                setSucess(true)
            })
    }
    else {
        setShake(true)
    }

    }

    useEffect(() => {
        const assoc = { ...usePedido1}
        assoc.trade = useTrade;
        setPedido1(assoc)
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useTrade])

    return (
        <Comanda ref={useSecond}>
            <Payment>
                <H3>Forma de pagamento</H3>
                <Info>Informe o mtodo de pagamento para que tenhamos uma entrega rpida e nosso atendimento seja eficaz. A entrega melhora muito com alguns detalhes importante.</Info>
                <Method>
                    <Checkradio>
                        <input type="checkbox" onChange={() => Input()} checked={useCash} />
                        <label onClick={() => setCash(prevStatus => !prevStatus)}>Pagar com dinheiro?</label>
                    </Checkradio>
                    <ComponentCashEdit { ...{ useCash, useContent, useSecond, useTrade, setTrade }} />
                </Method>
                <Method>
                    <Checkradio>
                        <input type="checkbox" onChange={() => Input()} checked={useCard} />
                        <label onClick={() => setCard(prevStatus => !prevStatus)}>Pagar no carto?</label>
                    </Checkradio>
                    <ComponentCardEdit { ...{ useCard, useContent, useSecond }} />
                </Method>
                {info.pix ?
                <Method>
                    <Checkradio>
                        <input type="checkbox" onChange={() => Input()} checked={usePix} />
                        <label onClick={() => setPix(prevStatus => !prevStatus)}>Pagar com Pix?</label>
                    </Checkradio>
                    <ComponentPixEdit { ...{ usePix, useContent, useSecond }} />
                </Method>
                : null }
            </Payment>

            <Span>Mais alguma coisa?</Span>
            <Textarea placeholder="Ex. interphone est com defeito" onChange={(event) => inputTextarea(event)} value={useTextarea}></Textarea>
            <Message>
                <em>Tenha o WhatsApp instalado em seu Smarthphone para prosseguir com o envio do pedido</em>
                <ButtonGreen useAfter={true} type="button" onClick={() => sendPedido()}>Enviar pedido!</ButtonGreen>
                <Strike><span>ou</span></Strike>
                <ButtonText type="button" onClick={() => setWindow(1)}>Voltar</ButtonText>
            </Message>
        </Comanda>
    )
}

export default ComponentModalSecond;