import styled from 'styled-components';

export const Methods = styled.div`
    padding: 0 20px;

    & > p {
        color: #000000;
        font-family: var(--font-open);
        font-weight: 900;
        font-size: 13px;
        padding: 4px 0;
    }

    & > div {
        display: flex;

        & > div {
            height: 20px;
            display: block;
            float: left;

            & img {
                border-radius: 3px;
            }
        }

        & > div:not(:last-child) {
            margin-right: 1.6rem;
        }
    }
`