import React from 'react';
import { SnapshotWindow, SnapshotAssoc, SnapshotBusiness } from '../ContextComanda';

import { Comanda, Line, Border, Strong, List, Subtotal, Frete, Footer, Message, ButtonGreen } from '../styles/Modal';

import ComponentPedidoDetail from './ComponentPedidoDetail';

function ComponentModalFirst({ useFirst }) {

    // eslint-disable-next-line no-unused-vars
    const { useWindow, setWindow } = SnapshotWindow()

    // eslint-disable-next-line no-unused-vars
    const { usePedido1, setPedido1 } = SnapshotAssoc()
    
    // eslint-disable-next-line no-unused-vars
    const {info, methods } = SnapshotBusiness()

    const { delivery, subvalue, detail } = usePedido1;

    const { brand, cnpj, address, number, optional, region, unity } = info;

    const Step = data => {
        setWindow(data)
    }

    return (
        <Comanda ref={useFirst}>
            <Line><b>{brand} Hamburgueria</b></Line>
            <Line>CNPJ {cnpj}</Line>
            <Line>{address}, {number} {optional !== '' ? `- ${optional} -` : '-'} {region} - {unity}</Line>
            <Border>Esta nota no substitui o cupom fiscal, portanto,  uma forma fcil de que o cliente est acostumado a visualizar os detalhes no pedido.</Border>
            <Strong>
                <span>#</span>
                <span>CDIGO</span>
                <span>DESCRIO</span>
                <span>QTD.</span>
                <span>TOTAL</span>
            </Strong>
            <List>
                <ComponentPedidoDetail detail={detail} />
                <Subtotal>
                    <span>SUBTOTAL</span>
                    <span>{Number(subvalue).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
                </Subtotal>
            </List>
            <Frete>
                <span><small>ACRESCIMO</small>VALOR DA ENTREGA</span>
                <span>{Number(delivery).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
            </Frete>
            <Footer>
                <b>TOTAL A PAGAR</b>
                <span>{Number(subvalue + delivery).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
            </Footer>
            <Message>
                <em>Tenha o WhatsApp instalado em seu Smarthphone para prosseguir com o envio do pedido</em>
                <ButtonGreen type="button" onClick={() => Step(2)}>Prximo!</ButtonGreen>
            </Message>
        </Comanda>
    )
}

export default ComponentModalFirst;