import React, { createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, usePedido1, setPedido1, info, methods, useWindow, setWindow, useSucess, setSucess }) {
    
    return (
        <ElementContext.Provider value={{ usePedido1, setPedido1, info, methods, useWindow, setWindow, useSucess, setSucess }}>
            {children}
        </ElementContext.Provider>
    );
}

export function SnapshotWindow() {
    const context = useContext(ElementContext)
    const { useWindow, setWindow } = context;
    return { useWindow, setWindow };
}

export function SnapshotBusiness() {
    const context = useContext(ElementContext)
    const { info, methods } = context;
    return { info, methods };
}

export function SnapshotAssoc() {
    const context = useContext(ElementContext)
    const { usePedido1, setPedido1 } = context;
    return { usePedido1, setPedido1 };
}

export function SnapshotSucess() {
    const context = useContext(ElementContext)
    const { useSucess, setSucess } = context;
    return { useSucess, setSucess };
}
