import React from 'react';

function ComponentExtraPedido({ extra }) {

    return extra[0].map((item, i) => {

        return extra[1][i][0] ? (
            <li key={i}>
                <span>--</span>
                <span>--</span>
                <span>Extra de {item[0]}</span>
                <span>{extra[1][i][0]}un</span>
                <span>{Number(item[3] * extra[1][i][0]).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
            </li>
        ) : null;
    })
}

export default ComponentExtraPedido;