import React, { createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, useContainer, useStage, setStage, useDelivery, useZone, setZone, setDelivery, useName, useTel, usePedido, setPedido, useRadio, setRadio }) {
    
    return (
        <ElementContext.Provider value={{ useContainer, useStage, setStage, useDelivery, setDelivery, useZone, setZone, useName, useTel, usePedido, setPedido, useRadio, setRadio }}>
            {children}
        </ElementContext.Provider>
    );
}

export function SnapshotComponents() {
    const context = useContext(ElementContext)
    const { useName, useTel, useContainer } = context;
    return { useName, useTel, useContainer };
}

export function SnapshotDelivery() {
    const context = useContext(ElementContext)
    const { useDelivery, setDelivery } = context;
    return { useDelivery, setDelivery };
}

export function SnapshotStage() {
    const context = useContext(ElementContext)
    const { useStage, setStage } = context;
    return { useStage, setStage };
}

export function SnapshotPedido() {
    const context = useContext(ElementContext)
    const { usePedido, setPedido } = context;
    return { usePedido, setPedido };
}

export function SnapshotRadio() {
    const context = useContext(ElementContext)
    const { useRadio, setRadio } = context;
    return { useRadio, setRadio };
}
