import React from 'react';
import ComponentLoopCard from '../components/ComponentLoopCard';
import { SnapshotConnection } from '../ContextConnection';

import { Page } from '../styles/Page';

function Porcoes() {

    // eslint-disable-next-line no-unused-vars
    const { GoConnection, EndConnection } = SnapshotConnection();
    const { feed } = GoConnection;

    const data = Object.entries(feed).filter(list => list[1].category.includes('Servings'))

    return data.length !== 0 ? (
        <ComponentLoopCard data={data} />
    ) : (
            <Page>
                <small>No h nenhuma poro publicada...</small>
            </Page>
        );
}

export default Porcoes;