import React from 'react';
import { SnapshotConnection } from '../ContextConnection';
import CarouselCard from '../components/ComponentCarouselCard';

import { BottomInfo, Backlinks } from '../styles/Footer';
import { useHistory } from 'react-router-dom';

function Home() {
  const history = useHistory();

  // eslint-disable-next-line no-unused-vars
  const { GoConnection, EndConnection } = SnapshotConnection();
  const { feed } = GoConnection;

  const ConvertArray = data => {
    return Object.entries(feed).filter(list => list[1].category.includes(data)).slice(0, 5)
  }

  return (
    <React.Fragment>
      <CarouselCard title="Hambrguer" data={ConvertArray('Burger')} para="/hamburguer" />
      <CarouselCard title="Promoes" data={ConvertArray('Promo')} para="/promocoes" />
      <CarouselCard title="Combos" data={ConvertArray('Combo')} para="/combos" />
      <CarouselCard title="Pores" data={ConvertArray('Servings')} para="/porcoes" />
      <CarouselCard title="Bebidas" data={ConvertArray('Drinks')} para="/bebidas" />
      <CarouselCard title="Bomboniere" data={ConvertArray('Bomboniere')} para="/bomboniere" />

      <BottomInfo>
        <div>Qualquer divergncia de valores dos produtos ou taxa de entrega poder ser ajustado direto com o estabelecimento.</div>
        <div>Todos os preos esto em BRL</div>
      </BottomInfo>

      <Backlinks>
        <div><button onClick={() => history.push('/termos')}>Termos &amp; Condies</button></div>
        <div><button onClick={() => history.push('/privacidade')}>Poltica de Privacidade</button></div>
        <div><button onClick={() => history.push('/cookies')}>Poltica de cookies</button></div>
        <div><button onClick={() => alert('Bracael.COM\nVerso Beta, 12.05.2020\nO Contedo para est pgina estar disponivel em breve.')}>Enviar feedback</button></div>
      </Backlinks>

    </React.Fragment>
  )
}

export default Home;