import styled from 'styled-components';

export const Page = styled.div`
    background: #ffffff;
    border-radius: 6px;
    box-shadow: rgba(0, 0, 0, 0.1) 0 0 16px;
    font-weight: 100;
    font-size: 1.5rem;
    width: 100%;
    
    & > small {
        width: 80%;
        display: flex;
        height: 30vh;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin: 0 auto;
    }
`