import React from 'react';
import ComponentLoopCard from '../components/ComponentLoopCard';
import { SnapshotConnection } from '../ContextConnection';

import { Page } from '../styles/Page';

function Destaque() {

    // eslint-disable-next-line no-unused-vars
    const { GoConnection, EndConnection } = SnapshotConnection();
    const { feed } = GoConnection;

    const data = Object.entries(feed)
    .filter(list => !list[1].category.includes('Drinks'))
    .slice(9)
    .sort((a, b) => a[1].purchase - b[1].purchase)
    .reverse()

    return data.length !== 0 ? (
        <ComponentLoopCard data={data} />
    ) : (
            <Page>
                <small>No h nenhum produto em destaque...</small>
            </Page>
        );
}

export default Destaque;