import React from 'react';
import { ItemCart } from '../ContextConnection';

import { Icon } from '../styles/Nav';

function ComponentIconCart() {

    // eslint-disable-next-line no-unused-vars
    const { useCart, setCart } = ItemCart();
    const data = Object.entries(useCart).map(elem => elem[1].amount).reduce((a, b) => a + b, 0);

    return data ? <Icon>{data}</Icon> : null;
}

export default ComponentIconCart;