import React, { useEffect, useRef, useState } from 'react';
import ReactDOM from 'react-dom';
import { SnapshotConnection, ItemCart } from '../ContextConnection';
import ElementProvider from '../ContextConfirm';

import ComponentStepFirst from '../components/ComponentStepFirst';
import ComponentStepSecond from '../components/ComponentStepSecond';
import ComponentStepThree from '../components/ComponentStepThree';

import { Container, Space, Order } from '../styles/Container';
import ComponentModalIngrediente from '../components/ComponentModalIngrediente';

const { jQuery: $ } = window;

function Confirma() {

    const useComponent = (() => {
        const Root = document.createElement('div')
        Root.setAttribute('role', 'modal');
        return Root
    })()

    const useContainer = useRef(null)
    const useChildConteiner = useRef(null)

    const useFirst = useRef(null)
    const useSecond = useRef(null)
    const useThree = useRef(null)

    const useName = useRef(null)
    const useTel = useRef(null)

    // eslint-disable-next-line no-unused-vars
    const [useStage, setStage] = useState(1)

    // eslint-disable-next-line no-unused-vars
    const [useRadio, setRadio] = useState('')

    // eslint-disable-next-line no-unused-vars
    const { GoConnection, EndConnection } = SnapshotConnection()
    const { business: { delivery, info } } = GoConnection;
    const { business } = GoConnection;

    const { useCart, setCart } = ItemCart()

    const resumeCart = Object.entries(useCart).map(elem => {
        return Number((elem[1].price * elem[1].amount) + elem[1].extra[1].map(item => {
            return item.length !== 0 ? item[0] !== 0 ? (item[0] * item[1]) : 0 : 0;
        }).reduce((a, b) => a + b, 0));
    }).reduce((a, b) => a + b, 0);

    // eslint-disable-next-line no-unused-vars
    const [usePedido, setPedido] = useState({
        client: '',
        comment: '',
        detail: Object.entries(useCart).map(data => {
            const id = data[0];
            const { amount, price, title, comment, extra } = data[1];
            const assoc = { id, amount, price, title, comment }

            const isValid = extra.every(val => val[0] && val[2] !== false && val[1] && val[2] !== '')
            const insertExtra = () => assoc.extra = extra;

            isValid && insertExtra()

            data[1].weight !== undefined ?
                assoc.weight = data[1].weight :
                assoc.grams = data[1].grams;

            return assoc;
        }),
        region: '',
        district: '',
        number: '', // numero da casa
        payment: [{
            cash: false,
            pix: false 
        },{
            debit: [],
            credit: [],
            voucher: [],
        }],
        street: '', // rua do cliente
        complement: '',
        tel: '', // numero de telefone
        time: new Date().toISOString(), // data do pedido
        subvalue: resumeCart, // subtotal do pedido
        delivery: 0,
        trade: 0
    })

    const [useDelivery, setDelivery] = useState(delivery)

    useEffect(() => {
        const { current: data } = useChildConteiner;
        const { current: first } = useFirst;
        const { current: second } = useSecond;
        const { current: three } = useThree;
        const item = useStage === 2 ? second : useStage === 3 ? three : first;
        // mantenha este comentario para no bugar a altura;
        $(data).height($(item).outerHeight(true))
    }, [useStage])

    
    const Inputs = {
        name: useName,
        tel: useTel
    }

    const startManage = () => {
        ReactDOM.render(<ComponentModalIngrediente { ...{ usePedido, setPedido, info, useCart, setCart }} />, document.body.appendChild(useComponent))
    }

    return business.status && business.info.minvalue <= resumeCart ? (
        <ElementProvider {...{ useContainer, useStage, setStage, useDelivery, setDelivery, Inputs, usePedido, setPedido, useRadio, setRadio }}>
            <Container ref={useContainer}>
                <div>
                    <Order>
                        <em>MANIPULE SEU PEDIDO!</em>
                        <p>Incremente ingredientes nos itens do seu carrinho ou tambm nos informe quando remover um ingrediente que tu no gosta.</p>
                        <button type="button" onClick={() => startManage()}>Manipular pedido</button>
                    </Order>
                    <Space stage={useStage} ref={useChildConteiner}>
                        <ComponentStepFirst {...{ useFirst }} />
                        <ComponentStepSecond {...{ useSecond }} />
                        <ComponentStepThree {...{ useThree }} />
                    </Space>
                </div>
            </Container>
        </ElementProvider>
    ) : business.status ? (
        <Container>
            <small>O valor mnimo de entregas  de {Number(info.minvalue).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</small>
        </Container>
    ) : (
                <Container>
                    <small>Estamos fechado no momento, volte quando o delivery estiver em aberto.</small>
                </Container>
            )
}

export default Confirma;