import React from 'react';
import { SnapshotSwipe } from '../ContextConnection';

import ComponentStatus from './ComponentStatus';
import ComponentTitle from './ComponentTitle';

import { Header, ChildHeader, ChildStatus, Marca, MenuButton, Menu } from '../styles/Header';
import ComponentMenu from './ComponentMenu';

function ComponentHeader() {

  // eslint-disable-next-line no-unused-vars
  const { useSwipe, setSwipe } = SnapshotSwipe();

  const OpenMenu = () => setSwipe('Pushmenu')

  return (
    <Header>
      <div>
        <ChildHeader>
          <section>
            <button type="button" onClick={() => alert('Por enquanto, no h nada por aqui.')}>
              <svg xmlns="http://www.w3.org/2000/svg" width="18" height="20" viewBox="0 0 18 20" fill="#5F6368"><rect y="14" width="18" height="3" rx="1.5" ry="1.5"/><path fillRule="evenodd" d="M8,18a2,2,0,0,0,4,0H8Z" transform="translate(-1)"/><rect x="7" width="4" height="6" rx="2" ry="2"/><path fillRule="evenodd" d="M3,14V8s0-5.995,7-6,7,6,7,6v6" transform="translate(-1)"/></svg>
            </button>
          </section>
          <Marca></Marca>
          <section>
            <MenuButton onClick={() => OpenMenu()} type="button">
              <Menu active={useSwipe} />
            </MenuButton>
          </section>
        </ChildHeader>
        <ChildStatus>
          <ComponentTitle />
          <ComponentStatus />
        </ChildStatus>
        <ComponentMenu />
      </div>
    </Header>
  )
}

export default ComponentHeader;