import React, { useEffect, useRef, useState } from 'react';
import Bracael from '../Bracael';
import { ItemCart, SnapshotSwipe } from '../ContextConnection';

import { Card, Image, PostImg, PostBttm, AddItem, Info, BttnsAndInput, Fieldset, Unavailable, ButtonsCrement, Description } from '../styles/Card';
import { ComponentButtonStart, ComponentButtonIncrement, ComponentButtonDecrement } from './ComponentButton';

function ComponentCard({ item }) {
    const useCrement = useRef(null)
    const useAdd = useRef(null)
    const useCount = useRef(null)

    const key = item[0];

    // eslint-disable-next-line no-unused-vars
    const { useSwipe, setSwipe } = SnapshotSwipe();

    // eslint-disable-next-line no-unused-vars
    const { useCart, setCart } = ItemCart();
    const [useDescription, setDescription] = useState(false);

    useEffect(() => {
        useDescription && setDescription(false)
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useSwipe])

    // eslint-disable-next-line no-unused-vars
    const [useExist, setExist] = useState(Object.keys(useCart).includes(key));

    const FormatImage = props => {
        const { protocol, hostname, pathname } = new URL(props);
        const PermisionUrl = ['i.imgur.com']

        return PermisionUrl.includes(hostname) ? `${protocol}//${hostname}${(() => {
            const splitPath = pathname.split('.')
            return `${splitPath[0]}l.${splitPath[1]}`
        })()}` : props;
    }

    return (
        <Card itemCategory={item[1].category}>
            <Image itemCategory={item[1].category}>
                <PostImg style={{ backgroundImage: `url(${FormatImage(item[1].thumb)})` }}>
                    <img src={item[1].category.includes('Drinks') ? 'data:image/gif;base64,R0lGODlhCgAPAAAAACH5BAEAAAEALAAAAAAKAA8AgM7t3M7t3AIKjI+py+0Po5xUFgA7' : 'data:image/gif;base64,R0lGODlhCgAHAAAAACH5BAEAAAEALAAAAAAKAAcAgM7t3M7t3AIHjI+py+2PCgA7'} alt={item[1].title} />
                    {item[1].category.includes('Drinks') ? null : <Description status={useDescription}><p>{item[1].description}</p></Description>}
                </PostImg>
                <PostBttm itemCategory={item[1].category}>
                    {item[1].category.includes('Unavailable') ? <Unavailable><span>Indisponvel</span></Unavailable> :
                        <React.Fragment>
                            <ButtonsCrement status={useExist} ref={useCrement}>
                                <BttnsAndInput>
                                    <Fieldset>
                                        <ComponentButtonDecrement { ...{ item, useCrement, useAdd, useCount, setExist }} />
                                    </Fieldset>
                                    <input type="number" min="1" max="5" disabled="disabled" autoComplete="off" value={useCart[item[0]] !== undefined ? useCart[item[0]].amount : 0} ref={useCount} />
                                    <Fieldset>
                                        <ComponentButtonIncrement { ...{ item, useCount }} />
                                    </Fieldset>
                                </BttnsAndInput>
                            </ButtonsCrement>
                            <AddItem status={useExist} ref={useAdd}>
                                <ComponentButtonStart { ...{ item, useCrement, useAdd, useCount, setExist }} />
                            </AddItem>
                        </React.Fragment>}
                </PostBttm>
            </Image>
            <Info itemCategory={item[1].category}>
                <span>{Number(item[1].price).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
                <div>
                    <h3>{item[1].title}</h3>
                    <span>{item[1].weight !== undefined ? Bracael.unitSize({ ml: item[1].weight, str: true }) : Bracael.unitSize({ gm: item[1].grams, str: true })}</span>
                </div>
                {item[1].category.includes('Drinks') ? null : <button type="button" onClick={() => setDescription(prevState => !prevState)}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" viewBox="0 0 524.235 524.235" fill="#fff"><path d="m262.118 0c-144.53 0-262.118 117.588-262.118 262.118s117.588 262.118 262.118 262.118 262.118-117.588 262.118-262.118-117.589-262.118-262.118-262.118zm17.05 417.639c-12.453 2.076-37.232 7.261-49.815 8.303-10.651.882-20.702-5.215-26.829-13.967-6.143-8.751-7.615-19.95-3.968-29.997l49.547-136.242h-51.515c-.044-28.389 21.25-49.263 48.485-57.274 12.997-3.824 37.212-9.057 49.809-8.255 7.547.48 20.702 5.215 26.829 13.967 6.143 8.751 7.615 19.95 3.968 29.997l-49.547 136.242h51.499c.01 28.356-20.49 52.564-48.463 57.226zm15.714-253.815c-18.096 0-32.765-14.671-32.765-32.765 0-18.096 14.669-32.765 32.765-32.765s32.765 14.669 32.765 32.765c0 18.095-14.668 32.765-32.765 32.765z" /></svg>
                </button>}
            </Info>
        </Card>
    )

}

export default ComponentCard;