import React from 'react';
import { SnapshotConnection } from '../ContextConnection';

// import { Container } from './styles';

function ComponentMethods() {

    // eslint-disable-next-line no-unused-vars
    const { GoConnection, EndConnection } = SnapshotConnection()
    const { business: { methods } } = GoConnection;

    const banrisul = <div><img height="20" src="https://1.bp.blogspot.com/-P6ong_vSPo4/X2hJ-EcfzbI/AAAAAAAAHPA/uMHLrlxGR7IlXURS3yBhpva8gOYlIhlHwCLcBGAsYHQ/s320/banrisul.png" alt="banrisul" /></div>
    const visa = <div><img height="20" src="https://4.bp.blogspot.com/-yjRJamc055o/XzKLkgO8A1I/AAAAAAAAHGo/EKk1YWu8uTwFHZmqtsKa7Q5XI_eXsMo3gCLcBGAsYHQ/s320/visa.png" alt="visa" /></div>;
    const maestro = <div><img height="20" src="https://4.bp.blogspot.com/-cP673wyKTYY/XzKNpfTn-5I/AAAAAAAAHG4/TYatZb6b4u8kqSQd56uk-2hP25N_-UeQgCLcBGAsYHQ/s320/maestro.png" alt="maestro" /></div>;
    const elo = <div><img height="20" src="https://2.bp.blogspot.com/-_cwtVG4dR1g/XzKRdsHr1BI/AAAAAAAAHHs/sz5v6dB6bYcjFd9aZFoEMF-RQQN59ATkQCLcBGAsYHQ/s320/elo.png" alt="elo" /></div>;
    const mastercard = <div><img height="20" src="https://4.bp.blogspot.com/-c2qRtCccgFk/XzKOp6dtO3I/AAAAAAAAHHQ/bksycs51u6EbTaF8_FMm31fN6iV6Hul-gCLcBGAsYHQ/s320/mastercard.png" alt="mastercard" /></div>;
    const hipercard = <div><img height="20" src="https://1.bp.blogspot.com/-Pxg-BfPny3o/X2hJ-NGAdpI/AAAAAAAAHO8/EepI7iQqVwEEi6Cf5RcDK2M2gzagl3HfwCLcBGAsYHQ/s320/hipercard.png" alt="hipercard" /></div>;
    const amex = <div><img height="20" src="https://3.bp.blogspot.com/-sf2IZ3tnMjM/XzKNe8H_PmI/AAAAAAAAHG0/zyjZveMaCbUJUDlVGI0x-dsIFK73Ys2EACLcBGAsYHQ/s320/amex.png" alt="amex" /></div>;
    const diners = <div><img height="20" src="https://3.bp.blogspot.com/-7W74XVvvqks/XzKPbSRz5jI/AAAAAAAAHHg/o4mhb7P8sR01FJkEhyWUUnsbS6Oy3Dd6QCLcBGAsYHQ/s320/diners.png" alt="diners" /></div>;
    const alelo = <div><img height="20" src="https://4.bp.blogspot.com/-oT3WobHsu4c/XzKUljudVSI/AAAAAAAAHII/FGkJNrJ6tbgDFwvH8EmxmrD20RNWjCiNwCLcBGAsYHQ/s320/alelo.png" alt="alelo" /></div>;
    const sodexo = <div><img height="20" src="https://2.bp.blogspot.com/-romfsWuFvDc/XzKTG2L-nvI/AAAAAAAAHH8/bzPh5XSrwwk3ArbFXPr05fyGHGVseoHqgCLcBGAsYHQ/s1600/sodexo.png" alt="sodexo" /></div>;
    const ticket = <div><img height="20" src="https://1.bp.blogspot.com/-N3flCB8it7Q/XzKWCU4HUyI/AAAAAAAAHIU/hwICpw0fQMk_RHUAoXW45it0ZSxsya40wCLcBGAsYHQ/s320/ticket.png" alt="ticket" /></div>;
    const vrref = <div><img height="20" src="https://1.bp.blogspot.com/-UpUHmGhhdL8/XzKKMZk6rDI/AAAAAAAAHGY/Nxt-U6lXPHY_eROYrvRynCJoMnvLeke1gCLcBGAsYHQ/s320/vr-ref.png" alt="vrref" /></div>;

    const cash = methods.cash.brl ? methods.cash.brl ? <small>BRL (moeda do Brasil)</small> : null : <small>No trabalhamos com dinheiro em espcie</small>;
    const debit = methods.debit.visa || methods.debit.maestro || methods.debit.elo || methods.debit.banrisul ?
    <React.Fragment>
        {methods.debit.visa ? visa : null}
        {methods.debit.maestro ? maestro : null}
        {methods.debit.elo ? elo : null}
        {methods.debit.banrisul ? banrisul : null}
    </React.Fragment> : <small>No trabalhamos com dbito</small>;
    const credit = methods.credit.visa || methods.credit.mastercard || methods.credit.elo || methods.credit.hipercard || methods.credit.amex || methods.credit.diners ?
    <React.Fragment>
        {methods.credit.visa ? visa : ''}
        {methods.credit.mastercard ? mastercard : ''}
        {methods.credit.elo ? elo : ''}
        {methods.credit.hipercard ? hipercard : ''}
        {methods.credit.amex ? amex : ''}
        {methods.credit.diners ? diners : ''}
    </React.Fragment> : <small>No trabalhamos com crdito</small>;
    const voucher = methods.voucher.alelo || methods.voucher.sodexo || methods.voucher.ticket || methods.voucher.vrref ?
    <React.Fragment>
        {methods.voucher.alelo ? alelo : ''}
        {methods.voucher.sodexo ? sodexo : ''}
        {methods.voucher.ticket ? ticket : ''}
        {methods.voucher.vrref ? vrref : ''}
    </React.Fragment> : <small>No trabalhamos com voucher</small>;

    return (
        <React.Fragment>
            <p>Dinheiro</p>
            <div>
                {cash}
            </div>
            <p>Carto de Dbito</p>
            <div>
                {debit}
            </div>
            <p>Carto de Crdito</p>
            <div>
                {credit}
            </div>
            <p>Voucher</p>
            <div>
                {voucher}
            </div>
        </React.Fragment>
    )
}

export default ComponentMethods;