import React from 'react';

import ComponentModalMethod from './ComponentModalMethod';
// import ComponentMethodCredit from './ComponentMethodCredit';

import { Modal, Close, Cash, CardForm, CashFooter, Informe } from '../styles/Modal';
import ElementProvider from '../ContextTypecard';

const { jQuery: $ } = window;

function ComponentModalEditCash({ info, methods, usePedido1, setPedido1 }) {

    // eslint-disable-next-line no-unused-vars
    const { debit, credit, voucher } = methods; //? method o que vem de "business > info"
    const isValidMethod = data => Object.entries(data).every(val => val[1] !== false)


    const modalClose = () => $('[role="edit"]').fadeOut(300, function () {
        this.remove();
    });

    $('[role="edit"]').fadeIn(300)

    const Guardar = () => modalClose()

    return (
        <ElementProvider { ...{ usePedido1, setPedido1 }}>
            <Modal>
                <Close onClick={() => modalClose()} />
                <Cash>
                    <Informe>
                        <svg xmlns="http://www.w3.org/2000/svg" height="18" width="18" viewBox="0 0 515.556 515.556"><path d="m257.778 0c-142.137 0-257.778 115.641-257.778 257.778s115.641 257.778 257.778 257.778 257.778-115.641 257.778-257.778-115.642-257.778-257.778-257.778zm32.222 386.667h-64.444v-64.444h64.444zm.002-96.667h-64.444v-161.111h64.444z" /></svg>
                        <span>Informe abaixo as bandeiras a ser utilizadas!</span>
                    </Informe>
                    <CardForm>
                        {isValidMethod(debit) ?
                            <React.Fragment>
                                <p>Carto de Dbito</p>
                                <div>
                                    {Object.entries(debit).map((item, i) => <ComponentModalMethod key={i} data="debit" {...{ item }} />)}
                                </div>
                            </React.Fragment> : null}
                        {isValidMethod(credit) ?
                            <React.Fragment>
                                <p>Carto de Crdito</p>
                                <div>
                                    {Object.entries(credit).map((item, i) => <ComponentModalMethod key={i} data="credit" {...{ item }} />)}
                                </div>
                            </React.Fragment> : null}
                        {isValidMethod(voucher) ?
                            <React.Fragment>
                                <p>Voucher</p>
                                <div>
                                    {Object.entries(voucher).map((item, i) => <ComponentModalMethod key={i} data="voucher" {...{ item }} />)}
                                </div>
                            </React.Fragment> : null}
                    </CardForm>
                    <CashFooter buttonOpacity={true}>
                        <button type="button" onClick={() => Guardar()}>Salvar</button>
                    </CashFooter>
                </Cash>
            </Modal>
        </ElementProvider>
    )
}

export default ComponentModalEditCash;

// useValue !== useTrade //! o valor global e local so diferentes?
// useValue  falso ( 0 ) ou verdadeiro ( maior que 0 )