import React, { useEffect, useRef, useState } from 'react';
import { SnapshotAssoc, SnapshotBusiness } from '../ContextComanda';

import { Edit } from '../styles/Container';

const { jQuery: $ } = window;

function ComponentPixEdit({ usePix, useContent, useSecond }) {

    const useComponent = useRef(null)
    const useSpan = useRef(null)
    const [useFree, setFree] = useState(false)

    // eslint-disable-next-line no-unused-vars
    const { info, methods } = SnapshotBusiness()

    // eslint-disable-next-line no-unused-vars
    const { usePedido1, setPedido1 } = SnapshotAssoc()
    // eslint-disable-next-line no-unused-vars
    const { payment: [{ cash, pix },{ debit, credit, voucher }] } = usePedido1;


    const WindowEffect = data => {
        setFree(data)

        const { current: content } = useContent;
        const { current: second } = useSecond;

        // mantenha este comentario para no bugar a altura;
        $(content).height($(second).outerHeight(true))
    }

    useEffect(() => {
        const { current } = useComponent;
        const startAnimate = () => $(current).animate({ height: 'toggle' }, 300, () => WindowEffect(true));
        useFree && startAnimate()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useFree])

    useEffect(() => {
        const { current } = useComponent;
        const closeAnimate = () => $(current).animate({ height: 'toggle' }, 300, () => WindowEffect(false));
        usePix ? setFree(true) : closeAnimate()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [usePix])

    const CopyPix = () => {
        const { current } = useSpan;
        current.focus();
        current.select();
      
        try {
          var successful = document.execCommand('copy');
          var msg = successful ? 'successful' : 'unsuccessful';
          console.log('Copying text command was ' + msg);
        } catch (err) {
          console.log('Oops, unable to copy');
        }
    }

    const Input = () => console.log('Input is declared but its value is never read.')

    return useFree ? (
        <Edit ref={useComponent}>
            <div>
                <textarea ref={useSpan} onChange={() => Input()} value={info.pix}></textarea>
                <button type="button" onClick={() => CopyPix()}>Copiar</button>
            </div>
        </Edit>
    ) : null;
}

export default ComponentPixEdit;