import React, { useEffect, useRef, useState } from 'react';
import { ItemCart, SnapshotAssoc, SnapshotDisplay } from '../ContextIngrediente';

import ComponentAdditional from './ComponentAdditional';

import { Li, Head, Body, Total, Textarea, Comment, Bottom } from '../styles/Manage';

const { jQuery: $ } = window;

function ComponentManage({ assoc, index }) {

    const useBody = useRef(null)

    // eslint-disable-next-line no-unused-vars
    const { useDisplay, setDisplay } = SnapshotDisplay()
    const [useLocal, setLocal] = useState(useDisplay === index)

    // eslint-disable-next-line no-unused-vars
    const { usePedido1, setPedido1 } = SnapshotAssoc()

    const { useCart, setCart } = ItemCart()

    // eslint-disable-next-line no-unused-vars
    const { id, amount, comment, grams, title, price, extra = null } = assoc;

    // eslint-disable-next-line no-unused-vars
    const [usePrice, setPrice] = useState(extra ? Number((price * amount) + extra[1].map((val) => val[0] * val[1]).reduce((total, numero) => total + numero, 0)) : Number(price * amount))

    const [useTextarea, setTextarea] = useState(comment)

    const inputTextarea = event => {
        const { target } = event;
        setTextarea(target.value)
    }

    const ToggleDisplay = () => useLocal ? setDisplay(-1) : setDisplay(index)

    const startAnimate = data => {
        const { current } = useBody;
        $(current).animate({ height: 'toggle' }, data)
    }

    useEffect(() => {
        const { current } = useBody;
        useLocal ? startAnimate(300) : current.hasAttribute('style') && startAnimate(200)
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useLocal])

    useEffect(() => {
        useDisplay === index ? setLocal(true) : setLocal(false)
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useDisplay])

    // eslint-disable-next-line no-unused-vars
    const [useAnimate, setAnimate] = useState(false)
    const [useShake, setShake] = useState(false)

    const setCommentGlobal = () => {
        setAnimate(true)
        setTimeout(() => setAnimate(false), 500);

        // altera o stado para o modal
        const Pedido = { ...usePedido1}
        Pedido.detail[index].comment = useTextarea;
        setPedido1(Pedido)
        
        // altera o stado para o global
        const Cart = { ...useCart}
        Cart[id].comment = useTextarea;
        setCart(Cart)
    }

    const animateComponentText = () => {
        setShake(true)
        setTimeout(() => {
            setShake(false)
        }, 500);
    }

    const saveComment = () => {
        useCart[id].comment !== useTextarea ?
        setCommentGlobal() : animateComponentText()
    }

    return (
        <Li>
            <Head>
                <p>
                    <span>{amount}un</span>
                    <span>{title}</span>
                </p>
                <button type="button" onClick={() => ToggleDisplay()}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="#e47a2e"><path fillRule="evenodd" data-name="Comment" d="M14,12H7L3,16V12H2a2,2,0,0,1-2-2V2A2,2,0,0,1,2,0H14a2,2,0,0,1,2,2v8A2,2,0,0,1,14,12ZM3,3H13V5H3V3ZM3,7H13V9H3V7Z" /></svg>
                </button>
            </Head>
            <Body ref={useBody}>
                {extra ?
                    <React.Fragment>
                        <h4>Adicionais?</h4>
                        <div>
                            {extra[0].map((assoc, i) => <ComponentAdditional key={i} { ...{ assoc, i, extra, id, price, amount, setPrice }} />)}
                            <Total>
                                <span>Total: {Number(usePrice).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
                            </Total>
                        </div>
                    </React.Fragment> : null}
                <h4>Algum comentrio?</h4>
                <Comment animeShake={useShake}>
                    <Textarea maxLength="140" placeholder="Ex: tirar a cebola, carne mal passada etc." onChange={(event) => inputTextarea(event)} value={useTextarea}></Textarea>
                    <Bottom animeBtn={useAnimate}>
                        <span>{useTextarea.length}/140</span>
                        <button type="button" onClick={() => saveComment()}>Salvar comentrio</button>
                    </Bottom>
                </Comment>
            </Body>
        </Li>
    )

}

export default ComponentManage;