import styled from 'styled-components';

export const MainComponent = styled.main`
    overflow-x: hidden;
    overflow-y: overlay;
    height: calc(100vh - 48px);

    & > div {
        max-width: 1040px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
    }
`

export const Content = styled.div`
    display: flex;
    flex-direction: column;
    padding: 0 1.6rem;
    margin-top: 2rem;
`