import React from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import { SnapshotSwipe } from '../ContextConnection';

import { Pushmenu, Pushbox, Marca, Footer, Lastupdate, Developer, Copyright } from '../styles/Pushmenu';
import { Semana } from '../styles/Semana';
import { Methods } from '../styles/Methods';
import ComponentMethods from './ComponentMethods';
import ComponentSemana from './ComponentSemana';

function ComponentPushmenu() {
    const history = useHistory();
    const { pathname } = useLocation()

    // eslint-disable-next-line no-unused-vars
    const { useSwipe, setSwipe } = SnapshotSwipe();

    const Call2Function = props => {
        pathname !== props && history.push(props);
        setSwipe('');
    }

    return (
        <Pushmenu active={useSwipe}>
            <Pushbox>
                <Marca></Marca>
                <article>
                    <ul>
                        <ol>
                            <li>
                                <button onClick={() => Call2Function('/')}>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 18 16" fill="#5F6368"><path fillRule="evenodd" d="M3,17V10H0L9,1l9,9H15v7H11V13H7v4H3Z" transform="translate(0 -1)" /></svg>
                                    <span>Pgina inicial</span>
                                </button>
                            </li>
                            <li>
                                <button onClick={() => Call2Function('/sobre')}>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 511.999 511.999" fill="#5F6368"><path d="M438.09,273.32h-39.596c4.036,11.05,6.241,22.975,6.241,35.404v149.65c0,5.182-0.902,10.156-2.543,14.782h65.461 c24.453,0,44.346-19.894,44.346-44.346v-81.581C512,306.476,478.844,273.32,438.09,273.32z" /><path d="M107.265,308.725c0-12.43,2.205-24.354,6.241-35.404H73.91c-40.754,0-73.91,33.156-73.91,73.91v81.581 c0,24.452,19.893,44.346,44.346,44.346h65.462c-1.641-4.628-2.543-9.601-2.543-14.783V308.725z" /><path d="M301.261,234.815h-90.522c-40.754,0-73.91,33.156-73.91,73.91v149.65c0,8.163,6.618,14.782,14.782,14.782h208.778 c8.164,0,14.782-6.618,14.782-14.782v-149.65C375.171,267.971,342.015,234.815,301.261,234.815z" /><path d="M256,38.84c-49.012,0-88.886,39.874-88.886,88.887c0,33.245,18.349,62.28,45.447,77.524 c12.853,7.23,27.671,11.362,43.439,11.362c15.768,0,30.586-4.132,43.439-11.362c27.099-15.244,45.447-44.28,45.447-77.524 C344.886,78.715,305.012,38.84,256,38.84z" /><path d="M99.918,121.689c-36.655,0-66.475,29.82-66.475,66.475c0,36.655,29.82,66.475,66.475,66.475 c9.298,0,18.152-1.926,26.195-5.388c13.906-5.987,25.372-16.585,32.467-29.86c4.98-9.317,7.813-19.946,7.813-31.227 C166.393,151.51,136.573,121.689,99.918,121.689z" /><path d="M412.082,121.689c-36.655,0-66.475,29.82-66.475,66.475c0,11.282,2.833,21.911,7.813,31.227 c7.095,13.276,18.561,23.874,32.467,29.86c8.043,3.462,16.897,5.388,26.195,5.388c36.655,0,66.475-29.82,66.475-66.475 C478.557,151.509,448.737,121.689,412.082,121.689z" /></svg>
                                    <span>Sobre a empresa</span>
                                </button>
                            </li>
                        </ol>
                        <ol>
                            <p>
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16" width="12" height="12" fill="#000000"><path d="m8 0c-4.41113 0-8 3.58887-8 8s3.58887 8 8 8 8-3.58887 8-8-3.58887-8-8-8zm1.41455 10.8286-2.41455-2.41455v-4.41406h2v3.58594l1.82861 1.82861z" /></svg>
                                <span>Horrio de atendimento</span>
                            </p>
                            <Semana>
                                <ComponentSemana />
                            </Semana>
                        </ol>
                        <ol>
                            <p>
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" width="12" height="12" fill="#000000"><g><path d="m512 163v-27c0-30.928-25.072-56-56-56h-400c-30.928 0-56 25.072-56 56v27c0 2.761 2.239 5 5 5h502c2.761 0 5-2.239 5-5z" /><path d="m0 205v171c0 30.928 25.072 56 56 56h400c30.928 0 56-25.072 56-56v-171c0-2.761-2.239-5-5-5h-502c-2.761 0-5 2.239-5 5zm128 131c0 8.836-7.164 16-16 16h-16c-8.836 0-16-7.164-16-16v-16c0-8.836 7.164-16 16-16h16c8.836 0 16 7.164 16 16z" /></g></svg>
                                <span>Formas de pagamento</span>
                            </p>
                            <Methods>
                                <ComponentMethods />
                            </Methods>
                        </ol>
                        <ol>
                            <li>
                                <button onClick={() => Call2Function('/politica')}>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="18" viewBox="0 0 16 18" fill="#5F6368"><path fillRule="evenodd" d="M3,0H13c1,1.006,2.995,2.993,4,4V16a2,2,0,0,1-2,2H3a2,2,0,0,1-2-2V2A2,2,0,0,1,3,0ZM4,4h6a1,1,0,0,1,0,2H4A1,1,0,0,1,4,4ZM4,8H14a1,1,0,0,1,0,2H4A1,1,0,0,1,4,8Zm0,4H14a1,1,0,0,1,0,2H4A1,1,0,0,1,4,12Z" transform="translate(-1)" /></svg>
                                    <span>Poltica de cancelamento</span>
                                </button>
                            </li>
                            <li>
                                <button onClick={() => Call2Function('/termos')}>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="18" viewBox="0 0 16 18" fill="#5F6368"><path fillRule="evenodd" d="M3,0H13c1,1.006,2.995,2.993,4,4V16a2,2,0,0,1-2,2H3a2,2,0,0,1-2-2V2A2,2,0,0,1,3,0ZM4,4h6a1,1,0,0,1,0,2H4A1,1,0,0,1,4,4ZM4,8H14a1,1,0,0,1,0,2H4A1,1,0,0,1,4,8Zm0,4H14a1,1,0,0,1,0,2H4A1,1,0,0,1,4,12Z" transform="translate(-1)" /></svg>
                                    <span>Termos de uso</span>
                                </button>
                            </li>
                        </ol>
                        <Footer>
                            <Copyright>FOODLOVER 2020 &#169;<br />Todos os direitos reservados.</Copyright>
                            <Developer>Desenvolvido por Bracael.COM</Developer>
                            <Lastupdate>Last time updated October 13, 2020</Lastupdate>
                        </Footer>
                    </ul>
                </article>
            </Pushbox>
        </Pushmenu>
    )
}

export default ComponentPushmenu;