import React from 'react';
import { SnapshotConnection, SnapshotSwipe } from '../ContextConnection';

import ComponentItensAttendance from './ComponentItensAttendance';

import { Attendance, Sub, Await } from '../styles/Attendance';

function ComponentAttendance() {

    // eslint-disable-next-line no-unused-vars
    const { GoConnection, EndConnection } = SnapshotConnection();
    const { order = {} } = GoConnection;

    // eslint-disable-next-line no-unused-vars
    const { useSwipe, setSwipe } = SnapshotSwipe();

    return (
        <Attendance active={useSwipe}>
            {Object.keys(order).length ?
            <div>
                <h3>Lista de atendimento</h3>
                <Sub>Total de {Object.keys(order).length} pedidos no momento</Sub>
                <Await>
                    <ComponentItensAttendance />
                </Await>
            </div>
            : <small>No h bairros registrados.</small>}
        </Attendance>
    )
}

export default ComponentAttendance;