import React from 'react';
import { SnapshotConnection } from '../ContextConnection';

// import { Container } from './styles';

function ComponentZoneDelivery() {

    // eslint-disable-next-line no-unused-vars
    const { GoConnection, EndConnection } = SnapshotConnection();
    const { business: { delivery } } = GoConnection;

    return Object.entries(delivery).sort(function (a, b) {
        return (a[1].zone > b[1].zone) ? 1 : ((b[1].zone > a[1].zone) ? -1 : 0);
    }).map(function(data, index, stuff){
        const letter = stuff[index-1] !== undefined ?
        data[1].zone.substring(0, 1) !== stuff[index-1][1].zone.substring(0, 1) ? stuff[index][1].zone.substring(0, 1) : '' :
        stuff[index][1].zone.substring(0, 1);

        return <div key={index}><span>{letter}</span><span>{data[1].zone}</span><span>{data[1].rate !== 0 ? Number(data[1].rate).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' }) : 'GRTIS'}</span></div>
    });
}

export default ComponentZoneDelivery;