import React, { createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, usePedido1, setPedido1, useDisplay, setDisplay, useCart, setCart }) {
    
    return (
        <ElementContext.Provider value={{ usePedido1, setPedido1, useDisplay, setDisplay, useCart, setCart }}>
            {children}
        </ElementContext.Provider>
    );
}

export function SnapshotAssoc() {
    const context = useContext(ElementContext)
    const { usePedido1, setPedido1 } = context;
    return { usePedido1, setPedido1 };
}

export function SnapshotDisplay() {
    const context = useContext(ElementContext)
    const { useDisplay, setDisplay  } = context;
    return { useDisplay, setDisplay };
}

export function ItemCart() {
    const context = useContext(ElementContext)
    const { useCart, setCart  } = context;
    return { useCart, setCart };
}
