import React, { useState } from 'react';
import { useLocation } from 'react-router-dom';

function ComponentTitle() {

    const { pathname } = useLocation();

    const [usePath, setPath] = useState(pathname)
    const [useTitle, setTitle] = useState('Carregando...')

    pathname !== usePath && setPath(pathname)

    if (usePath === '/') {
        useTitle !== 'Incio' && setTitle('Incio');
    }
    else if (usePath === '/sobre') {
        useTitle !== 'Sobre' && setTitle('Sobre');
    }
    else if (usePath === '/confirma') {
        useTitle !== 'Enviar pedido' && setTitle('Enviar pedido');
    }
    else if (usePath === '/edit') {
        useTitle !== 'Novo produto' && setTitle('Novo produto');
    }
    else if (usePath === '/termos') {
        useTitle !== 'Termos' && setTitle('Termos');
    }
    else if (usePath === '/politica') {
        useTitle !== 'Politica' && setTitle('Politica');
    }
    else {
        useTitle !== 'Cardpio' && setTitle('Cardpio');
    }
    return <h1>{useTitle}</h1>
}

export default ComponentTitle;