import React from 'react';
import { SnapshotConnection, SnapshotSwipe } from '../ContextConnection';

import ComponentZoneDelivery from './ComponentZoneDelivery';

import { District, Sub, Delivery } from '../styles/District';

function ComponentDistrict() {

    // eslint-disable-next-line no-unused-vars
    const { GoConnection, EndConnection } = SnapshotConnection();
    const { business: { delivery, info: { region, unity } } } = GoConnection;

    // eslint-disable-next-line no-unused-vars
    const { useSwipe, setSwipe } = SnapshotSwipe();

    return (
        <District active={useSwipe}>
            {Object.keys(delivery).length ?
            <div>
                <h3>rea de entrega</h3>
                <Sub>Resultados para <strong>{region}/{unity}</strong></Sub>
                <Delivery>
                    <ComponentZoneDelivery />
                </Delivery>
            </div>
            : <small>No h bairros registrados.</small>}
        </District>
    )
}

export default ComponentDistrict;