import styled from 'styled-components';

export const BottomInfo = styled.section`
    margin: 1rem 4rem;
    padding: 2.2rem 1.6rem 0;
    text-align: center;

    & div {
        text-align: center;
    }

    & div:last-child {
        margin-top: 12px;
    }
`

export const Backlinks = styled.section`
    margin-bottom: 1.6rem;
    font-family: var(--font-lato);
    display: block;
    padding: 1rem 1.8rem;
    text-align: center;

    & > div {
        display: inline-block;
        line-height: 16px;
        padding: 4px;

        & button:hover {
            text-decoration: underline;
        }
    }
`