import styled from 'styled-components';
import { Even, ScrollEffect } from './root';

export const District = styled.aside`
    width: 84vw;
    color: #000000;
    background: #ffffff;
    max-width: 320px;
    min-width: 290px;
    position: fixed;
    z-index: 11000;
    height: 100vh;
    left: 0;
    top: 0;

    ${({ active }) => active === 'District' ? `
    -webkit-transform: translate3d(0,0,0);
    -moz-transform: translate3d(0,0,0);
    -ms-transform: translate3d(0,0,0);
    -o-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
    ` : `
    -webkit-transform: translate3d(-100vw,0,0);
    -moz-transform: translate3d(-100vw,0,0);
    -ms-transform: translate3d(-100vw 0,0);
    -o-transform: translate3d(-100vw,0,0);
    transform: translate3d(-100vw,0,0);
    `}

    & {
    -webkit-transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    }

    & > div {
        display: grid;
        -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
        grid-template-rows: minmax(44px,0fr) 0fr 1fr;
        
        & > h3 {
            font-size: 2.2rem;
            font-weight: 300;
            text-align: center;
            padding: 1.6rem 0 .8rem;
        }
    }
`

export const Sub = styled.div`
    color: #ffffff;
    background-color: rgba(134, 134, 134, 0.75);
    border-color: rgba(134, 134, 134, 0.80);

    position: relative;
    margin: .75rem 20px;
    padding: .75rem 20px;
    margin-bottom: 1rem;
    border-style: solid;
    border-width: 0;
    border-radius: .25rem;
    border-left-width: 5px;
    font-size: 1.5rem;
    font-weight: 100;

    & strong {
        font-weight: 500;
    }
`

export const Delivery = styled.div`
    overflow-x: hidden;
    overflow-y: overlay;
    position: relative;
    height: calc(100vh - 74px);

    ${ScrollEffect}

    & > div {
        padding: 1.2rem 20px;
        position: relative;
        
        display: grid;
        grid-template-columns: minmax(12px, 0fr) 1fr 0fr;
        grid-template-rows: 1fr;
        grid-gap: 1.6rem;
        justify-content: space-between;

        &:nth-child(even) {
            ${Even}
        }

        & span:first-child, & span:last-child {
            font-weight: 500;
        }

        & span:last-child {
            color: #900000;
            font-size: 1.7rem;
        }
    }
`