import styled from 'styled-components';

import { ButtonEffect, ScrollEffect } from './root';

export const Cart = styled.aside`
    width: 84vw;
    color: #000000;
    background: #ffffff;
    max-width: 320px;
    min-width: 290px;
    position: fixed;
    z-index: 11000;
    height: 100vh;
    left: 0;
    top: 0;

    ${({ active }) => active === 'Cart' ? `
    -webkit-transform: translate3d(0,0,0);
    -moz-transform: translate3d(0,0,0);
    -ms-transform: translate3d(0,0,0);
    -o-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
    ` : `
    -webkit-transform: translate3d(-100vw,0,0);
    -moz-transform: translate3d(-100vw,0,0);
    -ms-transform: translate3d(-100vw 0,0);
    -o-transform: translate3d(-100vw,0,0);
    transform: translate3d(-100vw,0,0);
    `}

    & {
    -webkit-transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    }

    & > div {
        display: grid;
        padding: 1.6rem 0;
        grid-template-rows: 0fr 0fr 1fr 0fr;
        height: 100%;

        & > h3 {
            display: flex;
            text-transform: uppercase;
            padding: 0 1.6rem;
            align-items: center;
            justify-content: center;
            color: #e49359;
            height: 48px;
        }
    }

    & > small {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 500;
        height: 100%;
    }
`

export const Total = styled.div`
    font-size: 36px;
    font-weight: 400;
    flex-direction: column;
    display: flex;
    justify-content: center;

    & strong {
        font-size: 1.6rem;
        text-align: center;
        font-weight: 100;
    }

    & p {
        font-size: 4.5rem;
        position: relative;
        line-height: 42px;
        display: flex;
        justify-content: center;
        left: 2.5px;
        font-weight: 500;

        & span {
            font-size: 18px;
            top: -10px;
            left: -4px;
            position: relative;
        }
    }
`

export const Itens = styled.div`
    overflow-x: hidden;
    overflow-y: overlay;

    ${ScrollEffect}

    & > div {
        display: grid;
        margin: 0 1.6rem;
        position: relative;
        grid-template-columns: 0fr 1fr;

        &:not(:last-child) {
            margin-bottom: 1.6rem;
        }
    }
`

export const Image = styled.section`
    overflow: hidden;
    border-radius: 3px;
    display: flex;
    align-items: center;
    height: 70px;
    width: 96px;
    max-width: 96px;
    min-width: 96px;

    & img {
        width: 100%;
        float: left;
        transform: scale(1.5);
        margin-top: 6px;
    }
`

export const Info = styled.section`
    display: inline-grid;
    flex-direction: column;
    align-items: center;
    margin-left: 1.6rem;
`

export const Price = styled.span`
    color: #0fc548;
    position: relative;
    font-weight: 500;

    & small {
        background: #c00;
        color: #fff;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        text-align: center;
        position: absolute;
        font-size: 1.3rem;
        line-height: 2.2rem;
        font-style: normal;
        z-index: 1;
        top: -4px;
        margin-left: 6px;
    }

    & div {
        float: right;
        display: flex;
        margin-top: -4px;

        & > *:not(:last-child) {
            margin-right: 0.6rem;
        }
    }
`

export const Done = styled.div`
    display: flex;
    justify-content: center;
    padding-top: 1.6rem;

    & > div {
        width: 100%;
        max-width: 260px;
    }

    & button {
        overflow: hidden;
        font-weight: 500;
        letter-spacing: 0.4px;
        border-radius: 3px;
        position: relative;
        display: flex;
        transition: transform 0.3s, box-shadow 0.3s;
        justify-content: center;
        align-items: center;

        width: 100%;
        font-size: 18px;
        max-width: 260px;
        border-width: 0 0 4px;
        border-style: solid;
        border-color: rgba(0,0,0,.2);
        text-transform: uppercase;

        color: #fff;
        text-align: center;

        background: #e47a2e;
        line-height: 48px;

        & svg {
            margin-right: 12px;
        }

        &:hover {
            background: #f58d43;
            -webkit-box-shadow: 0 7px 14px rgba(50,50,93,.1), 0 3px 6px rgba(0,0,0,.08);
            -webkit-transform: translateY(-1px);
            box-shadow: 0 7px 14px rgba(50,50,93,.1), 0 3px 6px rgba(0,0,0,.08);
            transition: background 0.3s;
            transform: translateY(-1px);
        }

        &:active {
            background-color: #b3662f;
            transition: all 0s;
        }

        ${ButtonEffect}

    }
`