import React from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import { SnapshotSwipe } from '../ContextConnection';

import { Nav, Fire, Fireleft, Firemain, Fireright, Firebttm, Button } from '../styles/Nav';
import ComponentIconCart from './ComponentIconCart';

function ComponentNav() {
  const { pathname } = useLocation()
  const history = useHistory();

  // eslint-disable-next-line no-unused-vars
  const { useSwipe, setSwipe } = SnapshotSwipe();

  const historyPush = data => (pathname !== data) && history.push(data)

  return (
    <Nav>
      <li>
        <Button active={pathname === '/'} type="button" onClick={() => historyPush('/')}>
          <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 18 16" fill="#5F6368"><path fillRule="evenodd" d="M3,17V10H0L9,1l9,9H15v7H11V13H7v4H3Z" transform="translate(0 -1)"/></svg>
        </Button>
      </li>
      <li>
        <Button active={false} type="button" onClick={() => setSwipe('Attendance')}>
          <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 16 16" fill="#5F6368"><path d="m8 0c-4.41113 0-8 3.58887-8 8s3.58887 8 8 8 8-3.58887 8-8-3.58887-8-8-8zm1.41455 10.8286-2.41455-2.41455v-4.41406h2v3.58594l1.82861 1.82861z"/></svg>
        </Button>
      </li>
      <li>
        <Button active={false} type="button" onClick={() => historyPush('/destaque')}>
          <Fire>
            <Fireleft>
              <div />
              <div />
            </Fireleft>
            <Firemain>
              <div />
              <div />
            </Firemain>
            <Fireright>
              <div />
              <div />
            </Fireright>
            <Firebttm>
              <div />
            </Firebttm>
          </Fire>
        </Button>
      </li>
      <li>
        <Button active={false} type="button" onClick={() => setSwipe('Cart')}>
          <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 17.719 18" fill="#5F6368"><circle cx="5" cy="16" r="2"/><circle cx="13" cy="16" r="2"/><path fillRule="evenodd" d="M5,14s7.438,0.005,9,0a1.015,1.015,0,1,0,0-2c-4.431-.014-6.043-0.008-8,0-0.625.013-.305-0.487,0-1,0.565-.85.654-1,1-1,0.7-.008,5.5-0.006,7,0,1.649,0.007,5.206-8.059,3-8C15.2,1.98,5.742,2,5,2A2.522,2.522,0,0,0,3,0H1A0.982,0.982,0,0,0,0,1,0.955,0.955,0,0,0,1,2C2.462,2,4.023,4.075,4,7c-0.008.053,0,.7,0,1,0,0.651-.117.538-1,2-0.91,1.376-.986,1.578-1,2,0,1.991.551,1.979,2,2H5Z"/></svg>
          <ComponentIconCart />
        </Button>
      </li>
      <li>
        <Button active={false} type="button" onClick={() => setSwipe('District')}>
          <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 512 512" fill="#5F6368"><g><path d="M256,0c-56.32,0-102.4,46.08-102.4,102.4c0,76.8,102.4,204.8,102.4,204.8s102.4-128,102.4-204.8 C358.4,46.08,312.32,0,256,0z M256,153.6c-28.16,0-51.2-23.04-51.2-51.2c0-28.16,23.04-51.2,51.2-51.2s51.2,23.04,51.2,51.2 C307.2,130.56,284.16,153.6,256,153.6z"/></g><g><path d="M384,204.8l-12.8,2.56c-25.6,51.2-58.88,99.84-76.8,128l-40.96,61.44l-40.96-61.44c-17.92-28.16-51.2-76.8-76.8-128 L128,204.8L0,256v256l128-51.2L256,512l128-51.2L512,512V256L384,204.8z"/></g></svg>
        </Button>
      </li>
    </Nav>
  )
}

export default ComponentNav;