import React from 'react';
import { ItemCart, SnapshotSwipe } from '../ContextConnection';

import { Cart } from '../styles/Cart';
import ComponentCartContent from './ComponentCartContent';

function ComponentCart() {

    // eslint-disable-next-line no-unused-vars
    const { useCart, setCart } = ItemCart();
    // eslint-disable-next-line no-unused-vars
    const { useSwipe, setSwipe } = SnapshotSwipe();

    return  (
        <Cart active={useSwipe}>
            { Object.keys(useCart).length ?
            <ComponentCartContent /> :
            <small>Por enquanto, no h nada por aqui.</small> }
        </Cart>
    )
}

export default ComponentCart;