import styled from 'styled-components';

export const Pushmenu = styled.aside`
    width: 84vw;
    color: #000000;
    background: #ffffff;
    max-width: 320px;
    min-width: 290px;
    position: fixed;
    z-index: 11000;
    height: 100vh;
    left: 0;
    top: 0;

    ${({ active }) => active === 'Pushmenu' ? `
    -webkit-transform: translate3d(0,0,0);
    -moz-transform: translate3d(0,0,0);
    -ms-transform: translate3d(0,0,0);
    -o-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
    ` : `
    -webkit-transform: translate3d(-100vw,0,0);
    -moz-transform: translate3d(-100vw,0,0);
    -ms-transform: translate3d(-100vw 0,0);
    -o-transform: translate3d(-100vw,0,0);
    transform: translate3d(-100vw,0,0);
    `}

    & {
    -webkit-transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    }

    & article {
        display: flex;
        flex-direction: column;
        position: relative;
        height: calc(100vh - 56px);
    }

    & ol {
        border-bottom-width: 1px;
        border-bottom-style: solid;
        border-bottom-color: rgba(77, 77, 77, 0.30);
        padding: 12px 0;

        & > p {
            font-size: 15px;
            padding: 4px 20px 8px 20px;
            display: flex;
            align-items: center;

            & svg {
                margin-right: 8px;
            }
        }
    }

    li {
        position: relative;

        & button {
            width: 100%;
            color: #353535;
            text-decoration: none;
            display: flex;
            padding: 7px 10px 7px 26px;
            line-height: 26px;
            font-size: 1.75rem;
            -webkit-box-align: center;
            align-items: center;
            font-weight: 700;

            &:hover {
                background: rgba(0, 0, 0, 0.05);
            }

            & svg {
                margin-right: 2.25rem;
            }
        }
    }
`

export const Pushbox = styled.div`
    display: flex;
    flex-direction: column;

    & > * {
        width: 100%;
    }

    & article {
        display: flex;
        flex-direction: column;
        position: relative;
        height: calc(100vh - 56px);

        & ul {
            flex-grow: 1;
            overflow-x: hidden;
            overflow-y: overlay;
        }
    }
`

export const Marca = styled.section`
    background-image: url("data:image/svg+xml;base64,");

    background-size: 116px;
    background-repeat: no-repeat;
    background-position: 26px center;
    height: 56px;

    box-shadow: inset 0 -1px rgba(77, 77, 77, 0.30);
    min-height: 56px;
    max-height: 56px;
`

export const Footer = styled.div`
    font-family: var(--font-open);

    & span {
        color: #717171;
        letter-spacing: 0.5px;
    }

    & > * {
        display: block;
    }
`

export const Developer = styled.div`
    padding: 0 20px 6px;
    display: inline-block;
    color: #717171;
`

export const Lastupdate = styled.span`
    padding: 0 20px 16px;
    font-size: 10px;
`

export const Copyright = styled.span`
    padding: 32px 20px 16px;
`