import React, { useEffect, useRef, useState } from 'react';
import ElementProvider from '../ContextComanda';

import ComponentModalFirst from './ComponentModalFirst';
import ComponentModalSecond from './ComponentModalSecond';

import { Modal, Content, Close, Inset } from '../styles/Modal';
import ComponentModalSucess from './ComponentModalSucess';

const { jQuery: $ } = window;

function ComponentModalComanda({ usePedido, setPedido, info, methods }) {

    const useContent = useRef(null)
    const useFirst = useRef(null)
    const useSecond = useRef(null)

    // eslint-disable-next-line no-unused-vars
    const [useShake, setShake] = useState(false)
    const [useSucess, setSucess] = useState(false)

    // eslint-disable-next-line no-unused-vars
    const [usePedido1, setPedido1] = useState(usePedido)

    useEffect(() => {
        setPedido(usePedido1)
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [usePedido1])

    const animateComponentText = () => {
        setTimeout(() => {
            setShake(false)
        }, 500);
    }

    useEffect(() => {
        useShake && animateComponentText()
    }, [useShake])

    // eslint-disable-next-line no-unused-vars
    const [useWindow, setWindow] = useState(1)

    const modalClose = () => {
        $('[role="modal"]').fadeOut(300, function () {
            this.remove();
        });
    }

    $('[role="modal"]').fadeIn(300)

    useEffect(() => {
        if(!useSucess) {
            const { current: content } = useContent;
            const { current: first } = useFirst;
            const { current: second } = useSecond;
    
            const item = useWindow === 1 ? first : second;
    
            // mantenha este comentario para no bugar a altura;
            $(content).height($(item).outerHeight(true))
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useWindow])

    useEffect(() => {
        useSucess && setTimeout(() => {
            window.location.href = "/";
            modalClose()
        }, 5000)
    }, [useSucess])

    return (
        <ElementProvider { ...{ usePedido1, setPedido1, info, methods, useWindow, setWindow, useSucess, setSucess }}>
            <Modal>
                {useSucess ? null : <Close onClick={() => modalClose()} />}
                <Content ref={useContent} animeShake={useShake}>
                    {useSucess ? 
                    <ComponentModalSucess { ...{ useContent }} /> :
                    <Inset stageSelect={useWindow}>
                        <ComponentModalFirst { ...{ useFirst }} />
                        <ComponentModalSecond { ...{ useContent, useSecond, setShake }} />
                    </Inset>}
                </Content>
            </Modal>
        </ElementProvider>
    )
}

export default ComponentModalComanda;