import React, { memo } from 'react';
import OwlCarousel from 'react-owl-carousel';
import ComponentMenuItens from './ComponentMenuItens';

function ComponentMenu() {

    const state = {
        responsive: {
            0: {
                items: 1,
            },
            450: {
                items: 2,
            },
            600: {
                items: 3,
            },
            1000: {
                items: 4,
            },
        },
    }

    return (
        <OwlCarousel items={1}
            className="owl-theme"
            loop={false}
            nav={false}
            dots={false}
            autoplay={false}
            mergeFit={false}
            autoWidth={true}
            rewind={false}
            responsive={state.responsive}
            margin={22} >
                <ComponentMenuItens />
        </OwlCarousel>
    )
}

export default memo(ComponentMenu);