// import React from 'react';
import Bracael from '../Bracael';
import { CardSmall, Thumb, Info, InfoTitle, InfoPrice } from '../styles/Carousel';
import OwlCarousel from 'react-owl-carousel';

function ComponentHomeCard({ data }) {

    const state = {
        responsive:{
            0: {
                items: 1,
            },
            450: {
                items: 2,
            },
            600: {
                items: 3,
            },
            1000: {
                items: 4,
            },
        },
    }

    const HomeCard = () => {

        return data.map(item => {
        
            const FormatImage = item => {
                const { protocol, hostname, pathname } = new URL(item);
                return hostname === 'i.imgur.com' ? `${protocol}//${hostname}${(() => {
                    const splitPath = pathname.split('.')
                    return `${splitPath[0]}s.${splitPath[1]}`
                })()}` : item;
            };
    
            return (
                <CardSmall key={item[0]}>
                    <Thumb style={{ backgroundImage: `url(${FormatImage(item[1].thumb)})` }}>
                        <img src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEAAAEALAAAAAABAAEAgM7t3M7t3AICTAEAOw==" height="92" width="92" alt={item[1].title} />
                    </Thumb>
                    <Info>
                        <InfoTitle>{item[1].title}</InfoTitle>
                        <InfoPrice>
                            <span>{item[1].weight !== undefined ? Bracael.unitSize({ ml: item[1].weight }) : Bracael.unitSize({ gm: item[1].grams })}</span>
                            <span>{Number(item[1].price).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
                        </InfoPrice>
                    </Info>
                </CardSmall>
            )
        })

    }

    return (
        <OwlCarousel items={1}
        className="owl-theme"
        loop={false}
        nav={false}
        dots={false}
        autoplay={false}
        mergeFit={false}
        autoWidth={true}
        rewind={false}
        responsive={state.responsive}
        margin={data.length !== 1 ? 22 : 0} >
            <HomeCard data={data} />
        </OwlCarousel>
    )
    
}

export default ComponentHomeCard;