import React, { useState, useEffect, useRef } from 'react';
import ReactDOM from 'react-dom';
import { SnapshotAssoc, SnapshotBusiness } from '../ContextComanda';

import ComponentModalEditCard from './ComponentModalEditCard';

import { Edit } from '../styles/Container';

const { jQuery: $ } = window;

function ComponentCardEdit({ useCard, useContent, useSecond }) {
    
    //# CARD EDIT

    const createComponent = (() => {
        const Root = document.createElement('div')
        Root.setAttribute('role', 'edit');
        return Root
    })()

    const useComponent = useRef(null)

    // eslint-disable-next-line no-unused-vars
    const { info, methods } = SnapshotBusiness()

    // eslint-disable-next-line no-unused-vars
    const { usePedido1, setPedido1 } = SnapshotAssoc()
    // eslint-disable-next-line no-unused-vars
    const { payment: [{ cash, pix },{ debit, credit, voucher }] } = usePedido1;


    const [useFree, setFree] = useState(false)


    const WindowEffect = data => {
        setFree(data)

        const { current: content } = useContent;
        const { current: second } = useSecond;

        // mantenha este comentario para no bugar a altura;
        $(content).height($(second).outerHeight(true))
    }

    useEffect(() => {
        const { current } = useComponent;
        const startAnimate = () => $(current).animate({ height: 'toggle' }, 300, () => WindowEffect(true));
        useFree && startAnimate()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useFree])

    useEffect(() => {
        const { current } = useComponent;
        const closeAnimate = () => $(current).animate({ height: 'toggle' }, 300, () => WindowEffect(false));
        useCard ? setFree(true) : closeAnimate()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useCard])

    const strMethod = {
        "banrisul": {
            name: "Banrisul"
        },
        "visa": {
            name: "Visa"
        },
        "maestro": {
            name: "Maestro"
        },
        "elo": {
            name: "Elo"
        },
        "mastercard": {
            name: "Mastercard"
        },
        "hipercard": {
            name: "Hipercard"
        },
        "amex": {
            name: "American Express"
        },
        "diners": {
            name: "Diners Club"
        },
        "alelo": {
            name: "Alelo"
        },
        "sodexo": {
            name: "Sodexo"
        },
        "ticket": {
            name: "Ticket"
        },
        "vrref": {
            name: "Vale Refeio"
        }
    }

    // eslint-disable-next-line no-unused-vars
    const [useTypecard, setTypecard] = useState([ ...debit, ...credit, ...voucher ])

    const Typecard = []
    Object.entries(strMethod).map(data => useTypecard.includes(data[0]) && Typecard.push(data[1].name))

    useEffect(() => {
        setTypecard([ ...usePedido1.payment[1].debit, ...usePedido1.payment[1].credit, ...usePedido1.payment[1].voucher ])
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [usePedido1])

    const ModalCard = () => {
        ReactDOM.render(<ComponentModalEditCard { ...{ info, methods, usePedido1, setPedido1 }} />, document.body.appendChild(createComponent))
    }

    return useFree ? (
        <Edit ref={useComponent}>
            <div>
                <span>{Typecard.length ? Typecard.length > 1 ? `${Typecard.length} cartes selecionado!` : '1 carto selecionado' : 'Nenhum carto selecionado'}</span>
                <button type="button" onClick={() => ModalCard()}>Editar</button>
            </div>
        </Edit>
    ) : null;
}

export default ComponentCardEdit;