import React, { useRef, useEffect, useState }  from 'react';

import { Modal, Close, Cash, CashForm, CashFooter, Informe } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentModalEditCash({ useTrade, setTrade }) {

    const usePrice = useRef(null)

    // valor principal
    const [useValue, setValue] = useState(useTrade)

    // valor do input
    // eslint-disable-next-line no-unused-vars
    const [useInput, setInput] = useState(Number(useValue).toFixed(2))

    const modalClose = () => $('[role="edit"]').fadeOut(300, function () {
        this.remove();
    });

    $('[role="edit"]').fadeIn(300)

    useEffect(() => {
        const { current } = usePrice;
        current.value = useInput;
    }, [useInput])

    // mask  iniciado e o array no useEffect, garante que sempre ser uma vez
    useEffect(() => {
        const { current } = usePrice;
        $(current).mask('#.##0,00', {reverse: true});
    }, [usePrice])

    const isValid = useValue !== useTrade;

    const Guardar = () => {
        if(isValid) {
            setTrade(useValue)
            modalClose()
        }
    }
    
    const Input = event => {
        const { target } = event;
        // eslint-disable-next-line no-useless-escape
        setValue(Number(target.value.replace(/\./g,'').replace(/\,/g,'.')))
    }

    return (
        <Modal>
            <Close onClick={() => modalClose()} />
            <Cash>
                <Informe>
                    <svg xmlns="http://www.w3.org/2000/svg" height="18" width="18" viewBox="0 0 515.556 515.556"><path d="m257.778 0c-142.137 0-257.778 115.641-257.778 257.778s115.641 257.778 257.778 257.778 257.778-115.641 257.778-257.778-115.642-257.778-257.778-257.778zm32.222 386.667h-64.444v-64.444h64.444zm.002-96.667h-64.444v-161.111h64.444z"/></svg>
                    <span>Informe abaixo a quantia para troco!</span>
                </Informe>
                <CashForm>
                    <span>R$</span>
                    <input type="text" placeholder="0" ref={usePrice} onInput={(event) => Input(event)} />
                </CashForm>
                <CashFooter buttonOpacity={isValid}>
                    <button type="button" onClick={() => Guardar()}>Salvar</button>
                </CashFooter>
            </Cash>
        </Modal>
    )
}

export default ComponentModalEditCash;

// useValue !== useTrade //! o valor global e local so diferentes?
// useValue  falso ( 0 ) ou verdadeiro ( maior que 0 )