import styled from 'styled-components';

import { BorderRadius, TransitionShake } from './root';

export const Li = styled.li`
    font-weight: 500;
    padding: 1rem 0;

    &:not(:last-child) {
        border-bottom-width: 1px;
        border-bottom-style: dashed;
        border-bottom-color: initial;
        margin: 0.6rem 0 0;

        padding-bottom: 1.6rem !IMPORTANT;
    }
`

export const Head = styled.div`
    justify-content: space-between;

    &, & > p {
        display: flex;
    }

    & > p span:not(:last-child) {
        margin-right: 1.6rem;
    }

    & svg {
        float: left;
    }
`

export const Body = styled.div`
    display: none;

    & > h4:first-of-type {
        padding-top: 1rem;
    }
`

export const Additional = styled.div`
    display: grid;
    grid-template-columns: 1fr minmax(30%, 0fr);
    grid-template-rows: 1fr;
    grid-gap: 1.6rem;

    background: #efefef;
    border-radius: 3px;
    padding: 1rem;

    &:first-child {
        margin-top: 1rem;
    }

    &:not(:last-child) {
        margin-bottom: 1rem;
    }
`

export const Info = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;

    & strong {
        color: #23b04f;
    }
`

export const Handle = styled.div`
    display: flex;
    align-items: center;
    height: 26px;
    margin: auto 0;
    border-radius: 3px;
    transition: background 0.2s;
    box-shadow: inset 0 -2px 0 0 rgba(0, 0, 0, 0.25);

    background: ${({ animeBtn }) => animeBtn ? '#d62b2b' : '#2bd660'};

    & button, & input {
        width: 100%;
        height: 26px;
        color: #fff;
        font-weight: 100;
    }

    & input {
        text-align: center;
        font-family: var(--font-oswald);
        background: rgba(4, 4, 4, 0.18);
    }

    & button {
        max-width: 24px;
    }
`

export const Total = styled.div`
    text-align: right;
`

export const Textarea = styled.textarea`
    padding: .8rem;
    border-radius: 3px;
    font-size: 1.6rem;
    min-width: -webkit-fill-available;
    transition: border-color 0.3s, box-shadow 0.3s;
    min-height: 40px;
    resize: none;

    width: 100%;
    border: 1px solid #bbc0c4;
    margin: 1rem 0 0;

    &:focus {
        border-color: #f7c16c;
        box-shadow: 0 0 0 4px rgba(255, 140, 0, 0.15);
    }
`

export const Comment = styled.div`
    ${({ animeShake }) => animeShake ? TransitionShake : ``}
`

export const Bottom = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 5px;

    & span {
        font-family: var(--font-oswald);
    }

    & button {
        font-size: 10px;
        font-family: var(--font-open);
        font-weight: 400;
        letter-spacing: 0.5px;
        box-shadow: inset 0 -2px 0 rgba(0, 0, 0, 0.25);
        transition: background 0.3s;
        color: #ffffff;
        font-style: italic;
        padding: 6px 10px;
        text-align: center;

        background: ${({ animeBtn }) => animeBtn ? '#2bd660' : '#e47a2e'};

        ${BorderRadius(3)}
    }
`