import React from 'react';
import { SnapshotPedido, SnapshotRadio } from '../ContextConfirm';

function ComponentDistrictSelect(item, index, stuff) {

    // eslint-disable-next-line no-unused-vars
    const { usePedido, setPedido } = SnapshotPedido()
    const { useRadio, setRadio } = SnapshotRadio()

    const labelClick = data => {
        const [key, { rate, zone, district }] = data;
        const pedido = { ...usePedido}
        pedido.delivery = rate;
        pedido.region = zone;
        pedido.district = district;
        setRadio(key)
        setPedido(pedido)
    }

    const letter = stuff[index-1] !== undefined ?
    item[1].zone.substring(0, 1) !== stuff[index-1][1].zone.substring(0, 1) ? stuff[index][1].zone.substring(0, 1) : '' :
    stuff[index][1].zone.substring(0, 1);

    const Input = () => {
        console.log('Input is assigned a value but never used.')
    }

    return (
        <div key={item[0]}>
            <span>{letter}</span>
            <p>{item[1].zone}{item[1].district !== '' ? ` - ${item[1].district}` : ''}</p>
            <span>{item[1].rate ? Number(item[1].rate).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' }) : 'GRTIS'}</span>
            <input type="radio" onChange={() => Input()} checked={useRadio === item[0]} />
            <div></div>
            <label onClick={() => labelClick(item)}></label>
        </div>
    )
}

export default ComponentDistrictSelect;