import styled from 'styled-components';

import { BorderRadius, ButtonEffect, Checkbox, TransitionShake } from './root';

export const Modal = styled.div`
    display: flex;
    height: 100vh;
    width: 100vw;
    z-index: 1;
    justify-content: center;
    align-items: center;
`

export const Close = styled.div`
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    width: 100vw;
    height: 100vh;
    position: fixed;
    background: transparent;
`

export const Comanda = styled.div`
    font-family: var(--font-oswald);
    font-weight: 500;
    padding: 1.6rem;
    overflow-y: overlay;
    overflow-x: hidden;
    max-height: 90vh;

    & > * {
        width: 100%;
    }
`

export const Content = styled.div`
    background: #fff;
    z-index: 99;
    max-height: 90vh;
    position: relative;
    width: calc(100vw - 3.6rem);
    max-width: 500px;
    overflow: hidden;
    font-size: 1.43rem;
    box-shadow: inset 0 0 15px 0 rgba(0,0,0,0.24), 0 12px 15px 0 rgba(0,0,0,0.24);
    transition: .3s;

    height: ${({ autoHeight = false }) => autoHeight ? `${autoHeight}px` : ''};
    padding: ${({ padding = 0 }) => padding};
    overflow-y: ${({ overflowY = 'hidden' }) => overflowY};;

    ${BorderRadius(3)}

    ${({ animeShake }) => animeShake ? TransitionShake : ``}

`

export const Inset = styled.div`
    left: -100%;
    width: 200%;
    position: relative;
    transition: .3s;
    overflow: hidden;

    left:${({ stageSelect }) => stageSelect === 1 ? '0' : '-100%'};

    & > div {
        width: 50%;
        float: left;
    }
`

export const Line = styled.p`
    text-transform: uppercase;
    line-height: 1.6;
    text-align: center;
`

export const Border = styled.p`
    border-width: 1px 0;
    border-style: dashed;
    border-color: initial;
    padding: 0.7rem 0 1rem;
    margin: 0.6rem 0;
`

export const Strong = styled.strong`
    display: flex;

    & > span {
        width: 100%;
        
        &:not(:last-child) {
            margin-right: 1.6rem;
        }
        
        &:nth-child(1) {
            max-width: 20px;
        }
        
        &:nth-child(2) {
            max-width: 56px;
        }
        
        &:nth-child(4) {
            max-width: 24px;
        }

        &:nth-child(5) {
            max-width: 50px;
            text-align: right;
        }
    }
`

export const List = styled.div`
    border-width: 1px 0;
    border-style: dashed;
    border-color: initial;
    padding: 0.7rem 0 1rem;
    margin: 0.6rem 0;

    & li {
        display: flex;

        & span {
            width: 100%;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;

            &:not(:last-child) {
                margin-right: 1.6rem;
            }
            
            &:nth-child(1) {
                max-width: 20px;
            }
            
            &:nth-child(2) {
                max-width: 56px;
            }
            
            &:nth-child(4) {
                max-width: 24px;
            }

            &:nth-child(5) {
                max-width: 50px;
                text-align: right;
            }
        }
    }
`

export const Subtotal = styled.div`
    border-top-width: 1px;
    border-top-style: dashed;
    border-top-color: initial;
    padding: 0.9rem 0 0;
    margin: 0.9rem 0 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
`

export const Frete = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;

    & > span:nth-child(1) small {
        display: block;
    }
`

export const Footer = styled.div`
    border-top-width: 1px;
    border-top-style: dashed;
    border-top-color: initial;
    padding: 0.9rem 0 0;
    margin: 0.9rem 0 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    & > span {
        font-size: 2.2rem;
    }
`

export const Message = styled.div`
    margin-top: 1rem;

    & em {
        width: 70%;
        display: flex;
        margin: 0 auto;
        justify-content: center;
        text-align: center;
        margin-bottom: 1rem;
    }
`

export const ButtonGreen = styled.button`
    overflow: hidden;
    font-weight: 500;
    letter-spacing: 0.4px;
    border-radius: 3px;
    position: relative;
    display: flex;
    transition: transform 0.3s, box-shadow 0.3s;
    justify-content: center;
    align-items: center;

    margin: 0 auto;
    width: 100%;
    font-size: 14px;
    max-width: 80%;
    border-width: 0 0 2px;
    border-style: solid;
    border-color: #357f38;

    background: #4CAF50;
    line-height: 34px;

    color: #fff;
    text-align: center;

    ${({ useAfter }) => useAfter ? `
        ${ButtonEffect}
    ` : ``}

    &:hover {
        background: #4ac34f;
        transition: background 0.3s;
    }

    &:active {
        background-color: #3aa73e;
        transition: all 0s;
    }
`

export const ButtonText = styled.button`
    display: block;
    max-width: 120px;
    margin: 0 auto;
    text-align: center;
    background: transparent;
    text-transform: uppercase;
    border-radius: 3px;
    font-weight: 900;
    font-family: var(--font-poppins);
    letter-spacing: 0.4rem;
    font-size: 10px;
`

export const Strike = styled.strong`
    position: relative;
    margin: 0.8rem auto;
    text-align: center;
    display: block;
    max-width: 160px;

    &::before, &::after {
        content: "";
        width: 100%;
        height: 1px;
        background-color: #000;
        position: absolute;
        top: 50%;
        max-width: 36%;
        box-sizing: inherit;
    }

    &::before {
        left: 0;
    }

    &::after {
        right: 0;
    }

    & span {
        color: #000000;
        position: relative;
        z-index: 1;
        font-weight: 100;
        font-size: 16px;
    }
`

export const Textarea = styled.textarea`
    padding: .8rem;
    border-radius: 3px;
    font-size: 1.6rem;
    min-width: -webkit-fill-available;
    transition: border-color 0.3s, box-shadow 0.3s;
    min-height: 40px;
    resize: none;

    background: transparent;
    border: 1px solid #000;
    width: initial;
    margin: 0 0.6rem;
`

export const H3 = styled.h3`
    width: 100%;
    font-weight: 400;
    font-size: 2.2rem;
    text-align: center;
    padding: 0 0 1rem;
`

export const Span = styled.span`
    font-size: 2rem;
    display: block;
    text-align: center;
    padding: 0 0 1rem;
`

export const Payment = styled.div`
    padding-bottom: 0.7rem;
`

export const Info = styled.p`
    padding: 0 0 1.2rem;
`

export const Method = styled.div`
    font-family: var(--font-poppins);
`

export const Checkradio = styled.div`
    margin: 0.4rem 0 0.4rem;

    ${Checkbox}
`

export const Cash = styled.div`
    background: #fff;
    z-index: 99;
    max-height: 90vh;
    position: relative;
    max-width: 500px;
    overflow: hidden;
    font-size: 1.43rem;
    box-shadow: inset 0 0 15px 0 rgba(0,0,0,0.24), 0 12px 15px 0 rgba(0,0,0,0.24);
    transition: .3s;
    
    width: calc(100vw - 24%);

    ${BorderRadius(3)}
`

export const CashForm = styled.div`
    padding: 0 1.6rem;
    display: flex;
    justify-content: space-evenly;

    & span {
        font-size: 2.22rem;
    }

    & input {
        text-shadow: 0 0 0 #333;
        background: transparent;
        border-bottom: 2px solid;
        margin: 0 0.4rem;
        font-weight: 100;
        font-size: 3rem;
        max-width: 70%;
        min-width: .25em;
        text-indent: 10%;
    }
`

export const CardForm = styled.div`
    padding: 0 2.4rem;

    & > div {


        & input:checked ~ label::before {
            background-color: rgb(49, 151, 238);
            box-shadow: rgb(244, 244, 244) 0px 0px 0px 3px inset;
        }

        & input ~ label::before {
            content: "";
            background: rgb(244, 244, 244);
            border-radius: 100%;
            border: 1px solid rgb(180, 180, 180);
            display: inline-block;
            width: 1.4em;
            height: 1.4em;
            position: relative;
            margin-right: 1em;
            vertical-align: top;
            cursor: pointer;
            text-align: center;
            transition: all 250ms ease 0s;
        }

        & label {
            cursor: pointer;
        }



        & > div {
            display: flex;
        }

        & > div:not(:last-child) {
            margin-bottom: 1rem;
        }
    }

    & img {
        float: left;
        ${BorderRadius(3)}
    }

    & p {
        color: #000000;
        font-weight: 900;
        font-size: 1.6rem;
        padding: 1rem 0;
    }
`

export const CashFooter = styled.div`
    padding: 2rem;
    display: flex;
    justify-content: space-around;

    & button {
        width: 100%;
        color: #fff;
        border-bottom: 2px solid rgba(0, 0, 0, 0.15);
        padding: 1rem;
        margin: 0 1rem;
        font-weight: 500;
        border-radius: 3px;
        transition: 0.25s;
        
        ${({ buttonOpacity }) => buttonOpacity ? `
        background: #59c15d;
        ` : `
        background: #c2c2c2;
        `}
    }
`

export const Informe = styled.div`
    padding: 2rem;
    display: flex;
    justify-content: space-between;

    & span {
        width: 100%;
        margin-left: 1.6rem;
        font-family: 'Oswald';
        font-weight: 500;
        font-size: 1.6rem;
    }
`

export const Sucess = styled.div`
    padding: 70px 1.6rem;

    & > div {
        display: flex;
        flex-direction: column;
        align-items: center;

        &:not(:last-child) {
            margin-bottom: 2rem;
        }
    }
`

export const TextSucess = styled.div`

    & > *:not(:last-child) {
        margin-bottom: 2rem;
    }

    & span {
        font-size: 2.2rem;
    }

    & p {
        text-align: center;
    }
`