import styled from 'styled-components';

export const Semana = styled.div`
    padding: 0 20px;

    & li:not(:last-child) {
        margin-bottom: 4px;
    }

    & li {
        display: grid;
        grid-gap: 1.6rem;
        grid-template-columns: minmax(80px, 0fr) 1fr;
        grid-template-rows: 1fr;
        position: relative;

        & span:first-child {
            text-align: right;
            font-weight: 700;
        }
        
        & span {
            width: 100%;
        }
    }
`