import React, { useState } from 'react';
import { HashRouter } from 'react-router-dom';
import ElementProvider from './ContextConnection';

import { database } from './utils/BrowserConnection';

import ComponentMain from './components/ComponentMain';
import ComponentNav from './components/ComponentNav';
import ComponentPushmenu from './components/ComponentPushmenu';
import ComponentCart from './components/ComponentCart';
import ComponentDistrict from './components/ComponentDistrict';
import ComponentAttendance from './components/ComponentAttendance';
import ComponentSwipe from './components/ComponentSwipe';

function App(snapshot) {

    const [GoConnection, EndConnection] = useState(snapshot);

    /*  :------------------------------------:
        | REALTIME DATABASE
        :------------------------------------:  */

    database.ref().on('value', snapshot => {
        if (JSON.stringify(snapshot.val()) !== JSON.stringify(GoConnection)) {
            EndConnection(snapshot.val())
        }
    })

    return (
        <HashRouter>
            <ElementProvider {...{ GoConnection, EndConnection }}>
                <ComponentMain />
                <ComponentNav />
                <ComponentPushmenu />
                <ComponentCart />
                <ComponentAttendance />
                <ComponentDistrict />
                <ComponentSwipe />
            </ElementProvider>
        </HashRouter>
    );
}

export default App;
