import React, { createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, usePedido1, setPedido1 }) {
    
    return (
        <ElementContext.Provider value={{ usePedido1, setPedido1 }}>
            {children}
        </ElementContext.Provider>
    );
}

export function SnapshotPedido() {
    const context = useContext(ElementContext)
    const { usePedido1, setPedido1 } = context;
    return { usePedido1, setPedido1 };
}

