import styled from 'styled-components';

import { ScrollEffect, Even } from './root';

export const Attendance = styled.aside`
    width: 84vw;
    color: #000000;
    background: #ffffff;
    max-width: 320px;
    min-width: 290px;
    position: fixed;
    z-index: 11000;
    height: 100vh;
    left: 0;
    top: 0;

    ${({ active }) => active === 'Attendance' ? `
    -webkit-transform: translate3d(0,0,0);
    -moz-transform: translate3d(0,0,0);
    -ms-transform: translate3d(0,0,0);
    -o-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
    ` : `
    -webkit-transform: translate3d(-100vw,0,0);
    -moz-transform: translate3d(-100vw,0,0);
    -ms-transform: translate3d(-100vw 0,0);
    -o-transform: translate3d(-100vw,0,0);
    transform: translate3d(-100vw,0,0);
    `}

    & {
    -webkit-transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    }

    & > div {
        display: grid;
        -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
        grid-template-rows: minmax(44px,0fr) 0fr 1fr;
        
        & > h3 {
            font-size: 2.2rem;
            font-weight: 300;
            text-align: center;
            padding: 1.6rem 0 .8rem;
        }
    }
`

export const Sub = styled.div`
    color: #ffffff;
    background-color: rgba(134, 134, 134, 0.75);
    border-color: rgba(134, 134, 134, 0.80);

    position: relative;
    margin: .75rem 20px;
    padding: .75rem 20px;
    margin-bottom: 1rem;
    border-style: solid;
    border-width: 0;
    border-radius: .25rem;
    border-left-width: 5px;
    font-size: 1.5rem;
    font-weight: 100;

    & strong {
        font-weight: 500;
    }
`

export const Await = styled.div`
    counter-reset: mynum;
    overflow-x: hidden;
    overflow-y: scroll;
    height: calc(100vh - 88px);
    position: relative;

    ${ScrollEffect}

    & > div {
        padding: 1.2rem 20px;
        position: relative;

        & > div {
            font-weight: 700;
            display: grid;
            grid-gap: 1.6rem;
            grid-template-columns: minmax(50px, 0fr) 1fr;
            grid-template-rows: 1fr;
        }

        &::after {
            color: #c71a1a;
            content: counter(mynum) "# ";
            counter-increment: mynum;
            font-weight: 700;
            position: absolute;
            top: 2.6rem;
            right: 2.6rem;
        }
    }

    & > div:nth-child(even) {
        ${Even}
    }
`