import React from 'react';
import ComponentHeader from './ComponentHeader';
import ComponentRouters from './ComponentRouters';

import { MainComponent, Content } from '../styles/Mainscroll';

function ComponentMain() {
    return (
        <MainComponent>
            <div>
                <ComponentHeader />
                <Content>
                    <ComponentRouters />
                </Content>
            </div>
        </MainComponent>
    )
}

export default ComponentMain;