export const BorderRadius = props => `
    -webkit-border-radius: ${typeof props === "number" ? props+'px' : props};
    -moz-border-radius: ${typeof props === "number" ? props+'px' : props};
    -ms-border-radius: ${typeof props === "number" ? props+'px' : props};
    -o-border-radius: ${typeof props === "number" ? props+'px' : props};
    border-radius: ${typeof props === "number" ? props+'px' : props};
`

export const BoxSizing = props => `
    -webkit-box-sizing: ${props};
    -moz-box-sizing: ${props};
    -ms-box-sizing: ${props};
    -o-box-sizing: ${props};
    box-sizing: ${props};
`

export const ScrollEffect = `
    &::before, &::after {
        content: "";
        width: 100%;
        height: 1.6rem;
        display: block;
        position: -webkit-sticky;
        position: sticky;
        z-index: 5;
        left: 0;
    }

    &::before {
        top: 0;
        background: #ffffff;
        background: -moz-linear-gradient(bottom, #ffffff 0%, transparent 100%);
        background: -webkit-linear-gradient(bottom, #ffffff 0%,transparent 100%);
        background: linear-gradient(to bottom, #ffffff 0%,transparent 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#ffffff00',GradientType=0 );
    }

    &::after {
        bottom: 0;
        background: #ffffff;
        background: -moz-linear-gradient(top, #ffffff 0%, transparent 100%);
        background: -webkit-linear-gradient(top, #ffffff 0%,transparent 100%);
        background: linear-gradient(to top, #ffffff 0%,transparent 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#ffffff00',GradientType=0 );
    }
`

export const ButtonEffect = `
&::after {
    animation-duration: 4s;
    animation-name: changewidth;
    animation-iteration-count: infinite;
    animation-direction: normal;
    animation-delay: 4s;
}
&::after {
    background: #fff;
    content: " ";
    height: 100%;
    width: 100px;
    left: -100%;
    opacity: 0;
    position: absolute;
    top: 0;
    -webkit-clip-path: polygon(35% 0%,100% 0%,65% 100%,0% 100%);
    clip-path: polygon(35% 0%,100% 0%,65% 100%,0% 100%);
    transition: all 400ms ease-out infinite;
    z-index: 2;
}
`

export const Even = `
background: -moz-linear-gradient(45deg, rgba(0,0,0,0.08) 0%, rgba(0,0,0,0) 100%);
background: -webkit-linear-gradient(45deg, rgba(0,0,0,0.08) 0%,rgba(0,0,0,0) 100%);
background: linear-gradient(45deg, rgba(0,0,0,0.08) 0%,rgba(0,0,0,0) 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#14000000', endColorstr='#00000000',GradientType=1 );
`

export const Checkbox = `
    position: relative;
    display: inline-flex;

    & input {
        &:checked ~ label::before {
            color: #fff;
            border-color: rgba(25, 145, 107, 0.3);
            background-color: rgba(25, 145, 107, 0.6);
        }
        &:checked ~ label::after {
            background-color: #fff;
            -webkit-transform: translateX(2.2rem);
            transform: translateX(2.2rem);
        }

        & ~ label::after {
            background-color: #aebcca;
            border-radius: 1.5rem;
            -webkit-transition: background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,-webkit-transform 0.15s ease-in-out,-webkit-box-shadow 0.15s ease-in-out;
            transition: background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,-webkit-transform 0.15s ease-in-out,-webkit-box-shadow 0.15s ease-in-out;
            transition: transform 0.15s ease-in-out,background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,box-shadow 0.15s ease-in-out;
            transition: transform 0.15s ease-in-out,background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,box-shadow 0.15s ease-in-out,-webkit-transform 0.15s ease-in-out,-webkit-box-shadow 0.15s ease-in-out;
        }

        &:not(:disabled):active ~ label::before {
            color: #fff;
            border-color: #75e7c3;
            background-color: #75e7c3;
        }
        &:focus:not(:checked) ~ label::before {
            border-color: #4ae0b0;
        }
        &:focus ~ label::before {
            -webkit-box-shadow: 0 0 0 0.2rem rgba(25,145,107,0.25);
            box-shadow: 0 0 0 0.2rem rgba(25,145,107,0.25);
        }
    }

    & label {
        display: inline-flex;
        align-items: center;
        cursor: pointer;

        & {
            display: inline-flex;
            align-items: center;
            cursor: pointer;
        }
        &::before {
            pointer-events: all;
            border-radius: 1.8rem;
            margin-right: 1.2rem;
            float: left;
        }
        &::before {
            -webkit-transition: background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,-webkit-box-shadow 0.15s ease-in-out;
            transition: background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,-webkit-box-shadow 0.15s ease-in-out;
            transition: background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,box-shadow 0.15s ease-in-out;
            transition: background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,box-shadow 0.15s ease-in-out,-webkit-box-shadow 0.15s ease-in-out;
        }
        &::before {
            display: block;
            width: 5rem;
            height: 2.4rem;
            pointer-events: none;
            content: "";
            background-color: #fff;
            border: #aebcca solid 1px;
        }
        &::after {
            position: absolute;
            top: 0.4rem;
            left: .4rem;
            display: block;
            width: 2rem;
            height: 1.6rem;
            content: "";
            background: no-repeat 50% / 50% 50%;
        }
    }
`

export const TransitionShake = `
    -webkit-animation-timing-function: ease-in-out;
    animation-timing-function: ease-in-out;
    -webkit-animation-name: headShake;
    animation-name: headShake;

    animation-duration: 1s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
`