const { jQuery: $ } = window;

// eslint-disable-next-line import/no-anonymous-default-export
export default {
    u: {
        a: ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"],
        b: ["Domingo", "Segunda", "Tera", "Quarta", "Quinta", "Sexta", "Sbado"],
        c: ["Domingo", "Segunda-feira", "Tera-feira", "Quarta-feira", "Quinta-feira", "Sexta-feira", "Sbado"],
        d: ["sun", "mon", "tue", "wed", "thu", "fri", "sat"],
        e: new Date().getDay()
    }, // formata o valor de medida de peso
    ConvertCart: function(data) {
        const key = data[0];
        const { title, thumb, price, weight, grams, extra } = data[1];

        const ITEM_CART = {
            [key] : {
                amount: 1,
                comment: '',
                moment: new Date().toISOString(),
                price,
                thumb,
                title,
            }
        }

        weight !== undefined ?
        (ITEM_CART[key].weight = weight) :
        (ITEM_CART[key].grams = grams);

        extra !== undefined ?
        (ITEM_CART[key].extra = [extra, extra.map(item=> [0, item[3]])]) :
        (ITEM_CART[key].extra = [[],[]])

        return ITEM_CART;
    },
    unitSize: function({ gm, ml, space = false, str = false }) {

/*  :------------------------------------:
    | REALTIME DATABASE
    | gm = unidade de medida para gramas
    | ml = unidade de medida para mililitros
    | space = um espao de uma letra inial
    | str = mostrar texto quando valor vazio
    :------------------------------------:  */

    // Peso indefinido : significa que o valor esperado no  um nmero, acontecer devido algum erro ao subir o valor para o banco de dados

        return (typeof gm === 'number' || typeof ml === 'number') ?
        ml === undefined ? gm >= 1000 ? (function(){

            var countGrams = 0;
            var atual = gm;
            function decrementAgain(){
                countGrams = ++countGrams;
                atual = atual - 1000;
                // eslint-disable-next-line no-unused-expressions
                atual >= 1000 ? decrementAgain() : null;
            }

            // eslint-disable-next-line no-unused-expressions
            atual >= 1000 ? decrementAgain() : null;

            if(atual !== 0){
                return space ? ` ${Number(countGrams+'.'+atual).toFixed(1).replace('.',',')}kg` : `${Number(countGrams+'.'+atual).toFixed(1).replace('.',',')}kg`;
            }
            else {
                return space ? ` ${countGrams}kg` : `${countGrams}kg`;
            }
        })() : gm !== 0 ? space ? ` ${gm}g` : ` ${gm}g` : str ? 'Peso no informado' : '' : ml >= 1000 ? (function(){

            var countGrams = 0;
            var atual = ml

            function decrementAgain(){
                countGrams = ++countGrams;
                atual = atual - 1000
                // eslint-disable-next-line no-unused-expressions
                atual >= 1000 ? decrementAgain() : null;
            }

            // eslint-disable-next-line no-unused-expressions
            atual >= 1000 ? decrementAgain() : null

            if(atual !== 0){
                return space ? ` ${Number(countGrams+'.'+atual).toFixed(1).replace('.',',')}L` : `${Number(countGrams+'.'+atual).toFixed(1).replace('.',',')}L`;
            }
            else {
                return space ? ` ${countGrams}L` : `${countGrams}L`;
            }
        })() : space ? ` ${ml}ml` : `${ml}ml` : str ? 'Peso indefinido' : '';
    },
    btnAnimate: function({ first, second, action, bolean = true }) {

        const INCREMENTdiv = bolean ? first : second;
        const GETTINGdiv = bolean ? second : first;

        $(INCREMENTdiv).animate({ opacity: 0 }, {
            duration: 400,
            easing: 'linear',
            queue: false
        });

        $(INCREMENTdiv).animate({
            'transform': '26'
        }, {
            step: function (now, fx) {
                $(this).css({ "transform": "translate3d(0px, " + now + "px, 0px)" });
            },
            duration: 400,
            easing: 'linear',
            queue: false,
            complete: function() {
                // action()
                // this.removeAttribute('style')
            }
        }, 'linear');

        setTimeout(function () {
            $(GETTINGdiv).fadeIn(400, function () {
                action()
                GETTINGdiv.removeAttribute('style')
                INCREMENTdiv.removeAttribute('style')
            });
        }, 200);
    }
}