import React from 'react';
// import $ from 'jquery';
import { Route, Switch, useLocation } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';

import Home from '../routes/Home';
import Hamburguer from '../routes/Hamburguer';
import Promocoes from '../routes/Promocoes';
import Combos from '../routes/Combos';
import Porcoes from '../routes/Porcoes';
import Bebidas from '../routes/Bebidas';
import Bomboniere from '../routes/Bomboniere';
import Confirma from '../routes/Confirma';
import Sobre from '../routes/Sobre';
import Termos from '../routes/Termos';
import Politica from '../routes/Politica';
import Destaque from '../routes/Destaque';

const { jQuery: $ } = window;

function ComponentRouters() {

  const { pathname } = useLocation()
  const pathroute = ['/', '/hamburguer', '/promocoes', '/combos', '/porcoes', '/bebidas', '/bomboniere', '/destaque']

  if(!pathroute.includes(pathname)){
      $('.owl-carousel').animate({ opacity: 0 }, {
        duration: 400,
        easing: 'linear',
        queue: false
      });
    
      $('.owl-carousel').animate({ height: 0 }, 600, function(){
        this.style.display = 'none';
        this.style.height = '';
      })
  }
  else {
      if(document.querySelector('.owl-carousel') && document.querySelector('.owl-carousel').style.display === 'none'){
        $('.owl-carousel').animate({ opacity: 1 }, {
          duration: 400,
          easing: 'linear',
          queue: false
        });

        $('.owl-carousel').animate({ height: 'toggle' }, 600, function(){
            this.removeAttribute('style')
        });
      }
  }

  const motionSetting = {
    mostrar: {
      zIndex: 1,
      x: 0,
      opacity: 1,
      transition: {
        type: "spring",
        damping: 14,
        stiffness: 230,
      },
    },
    esconder: {
      zIndex: 0,
      x: -200,
      opacity: 0.1,
      transition: {
        duration: 4,
      },
    },
    inicial: {
      x: "5%",
      opacity: 1,
    },
  }

  const RenderComponent = ({ children }) => (
    <motion.div variants={motionSetting} animate="mostrar" initial="inicial" exit="esconder">
      {children}
    </motion.div>
  )

  return (
    <AnimatePresence>
      <Switch>
        <Route path="/" exact render={() => <RenderComponent children={<Home />} />} />
        <Route path="/hamburguer" render={() => <RenderComponent children={<Hamburguer />} />} />
        <Route path="/promocoes" render={() => <RenderComponent children={<Promocoes />} />} />
        <Route path="/combos" render={() => <RenderComponent children={<Combos />} />} />
        <Route path="/porcoes" render={() => <RenderComponent children={<Porcoes />} />} />
        <Route path="/bebidas" render={() => <RenderComponent children={<Bebidas />} />} />
        <Route path="/bomboniere" render={() => <RenderComponent children={<Bomboniere />} />} />
        <Route path="/confirma" render={() => <RenderComponent children={<Confirma />} />} />
        <Route path="/sobre" render={() => <RenderComponent children={<Sobre />} />} />
        <Route path="/termos" render={() => <RenderComponent children={<Termos />} />} />
        <Route path="/politica" render={() => <RenderComponent children={<Politica />} />} />
        <Route path="/destaque" render={() => <RenderComponent children={<Destaque />} />} />
      </Switch>
    </AnimatePresence>
  );
}

export default ComponentRouters;