import React, { useState, useEffect, useRef } from 'react';
import ReactDOM from 'react-dom';
import { SnapshotAssoc } from '../ContextComanda';

import ComponentModalEditCash from './ComponentModalEditCash';

import { Edit } from '../styles/Container';

const { jQuery: $ } = window;

function ComponentCashEdit({ useCash, useContent, useSecond, useTrade, setTrade }) {

    //! CASH EDIT

    const createComponent = (() => {
        const Root = document.createElement('div')
        Root.setAttribute('role', 'edit');
        return Root
    })()
    
    const useComponent = useRef(null)
    
    // eslint-disable-next-line no-unused-vars
    const { usePedido1, setPedido1 } = SnapshotAssoc()
    const { trade } = usePedido1;

    const [useFree, setFree] = useState(false)
    
    useEffect(() => {
        setTrade(trade)
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useCash])

    const WindowEffect = data => {
        setFree(data)

        const { current: content } = useContent;
        const { current: second } = useSecond;

        // mantenha este comentario para no bugar a altura;
        $(content).height($(second).outerHeight(true))
    }

    useEffect(() => {
        const { current } = useComponent;
        const startAnimate = () => $(current).animate({ height: 'toggle' }, 300, () => WindowEffect(true));
        useFree && startAnimate()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useFree])

    useEffect(() => {
        const { current } = useComponent;
        const closeAnimate = () => $(current).animate({ height: 'toggle' }, 300, () => WindowEffect(false));
        useCash ? setFree(true) : closeAnimate()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useCash])

    const ModalPrice = () => {
        ReactDOM.render(<ComponentModalEditCash { ...{ useTrade, setTrade }} />, document.body.appendChild(createComponent))
    }

    return useFree ? (
        <Edit ref={useComponent}>
            <div>
                <span>{useTrade.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
                <button type="button" onClick={() => ModalPrice()}>Troco?</button>
            </div>
        </Edit>
    ) : null;
}

export default ComponentCashEdit;