import React from 'react';
import { useLocation, useHistory } from 'react-router-dom';
import { ItemCart, SnapshotSwipe } from '../ContextConnection';

import { Total, Itens, Done } from '../styles/Cart';
import ConsponentItensCart from './ConsponentItensCart';

function ComponentCartContent() {
    const { pathname } = useLocation()
    const history = useHistory();

    // eslint-disable-next-line no-unused-vars
    const { useCart, setCart } = ItemCart();
    // eslint-disable-next-line no-unused-vars
    const { useSwipe, setSwipe } = SnapshotSwipe();

    const ReduceCart = Object.entries(useCart).map(elem => Number((elem[1].price * elem[1].amount) + elem[1].extra[1].map(extra => extra.length !== 0 ? extra[0] !== 0 ? Number(extra[0] * extra[1]) : 0 : 0).reduce((total, numero) => total + numero, 0))).reduce((acumulador, valorAtual) => acumulador + valorAtual, 0);

    const Confirm = () => {
        history.push('/confirma')
        setSwipe('')
    }

    return (
        <div>
            <h3>Todos os seus pedidos</h3>
            <Total>
                <strong>subtotal a pagar</strong>
                <p><span>R$</span>{Number(ReduceCart).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' }).replace('R$', '').trim()}</p>
            </Total>
            <Itens>
                <ConsponentItensCart />
            </Itens>
            {pathname !== '/confirma' ? <Done>
                <div>
                    <button type="button" onClick={() => Confirm()}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 78.369 78.369" fill="#fff"><path d="M78.049,19.015L29.458,67.606c-0.428,0.428-1.121,0.428-1.548,0L0.32,40.015c-0.427-0.426-0.427-1.119,0-1.547l6.704-6.704 c0.428-0.427,1.121-0.427,1.548,0l20.113,20.112l41.113-41.113c0.429-0.427,1.12-0.427,1.548,0l6.703,6.704 C78.477,17.894,78.477,18.586,78.049,19.015z" /></svg>
                        <span>Confirmar</span>
                    </button>
                </div>
            </Done> : null}
        </div>
    )
}

export default ComponentCartContent;