import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
// import './index.css';

import { database } from './utils/BrowserConnection';

const StartConnection = () => Promise.resolve(database.ref().once('value').then(snapshot=>snapshot.val()))

StartConnection()
.then(function(data){
  const { hostname, protocol } = new URL(window.location.href);
  // console.log(hostname, protocol)
  // hostname === 'www.foodlover.poa.br' && protocol === 'https:' // validar para produo !IMPORTANT
  // console.log(hostname === 'localhost' && protocol === 'http:')

  return hostname === 'www.foodlover.poa.br' && protocol === 'https:' && data !== undefined ? Promise.resolve(data) : Promise.reject()
})
.then(function(snapshot){
  ReactDOM.render(
    <React.StrictMode>
      <App {...snapshot} />
    </React.StrictMode>, document.getElementById('root'));
})
.catch(function(){
  ReactDOM.render(<div>ERRO 500</div>, document.getElementById('root'));
})