import React from 'react';
import { SnapshotConnection } from '../ContextConnection';

import { Status } from '../styles/Status';

function ComponentStatus() {

    // eslint-disable-next-line no-unused-vars
    const { GoConnection, EndConnection } = SnapshotConnection()
    const { business: { status } } = GoConnection;

    return (
        <Status { ...{ status }} >{status ? 'Delivery aberto!' : 'Delivery fechado!'}</Status>
    )
}

export default ComponentStatus;