import React from 'react';
import { useHistory } from 'react-router-dom';
import ComponentHomeCard from './ComponentHomeCard';

import { CarouselHome, SlideCarousel } from '../styles/Carousel';

function CarouselCard({ title, data, para }) {
    const history = useHistory();

    return data.length ? (
        <CarouselHome dataLength={data.length} >
            <button type="button" onClick={() => history.push(para)}>
                {title}
                <svg id="ARROWrightIcon" xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 306 306"><polygon points="94.35,0 58.65,35.7 175.95,153 58.65,270.3 94.35,306 247.35,153" /></svg>
            </button>
            <SlideCarousel>
                <ComponentHomeCard data={data} />
            </SlideCarousel>
        </CarouselHome>
    ) : null;
}

export default CarouselCard;