import React, { useState, createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, GoConnection, EndConnection }) {

    // CARRINHO REATIVO
    const [useSwipe, setSwipe] = useState('')
    const [useCart, setCart] = useState(() => {
        const { itemCart = '{}' } = sessionStorage;
        return JSON.parse(itemCart);
    });

    sessionStorage.setItem('itemCart', JSON.stringify(useCart))
    
    return (
        <ElementContext.Provider value={{ GoConnection, EndConnection, useCart, setCart, useSwipe, setSwipe }}>
            {children}
        </ElementContext.Provider>
    );
}

export function SnapshotConnection() {
    const context = useContext(ElementContext)
    const { GoConnection, EndConnection } = context;
    return { GoConnection, EndConnection };
}

export function ItemCart() {
    const context = useContext(ElementContext)
    const { useCart, setCart } = context;
    return { useCart, setCart };
}

export function SnapshotSwipe() {
    const context = useContext(ElementContext)
    const { useSwipe, setSwipe } = context;
    return { useSwipe, setSwipe };
}