import React, { useRef } from 'react';
import ReactDOM from 'react-dom';
import { SnapshotConnection } from '../ContextConnection';
import { SnapshotStage, SnapshotPedido } from '../ContextConfirm';

import ComponentModalComanda from './ComponentModalComanda';

import { Group, Alert, Bottom } from '../styles/Container';

function ComponentStepThree({ useThree }) {

    const useStreet = useRef(null)
    const useNumber = useRef(null)

    // eslint-disable-next-line no-unused-vars
    const { useStage, setStage } = SnapshotStage()
    const { usePedido, setPedido } = SnapshotPedido()

    const useComponent = (() => {
        const Root = document.createElement('div')
        Root.setAttribute('role', 'modal');
        return Root
    })()

    // eslint-disable-next-line no-unused-vars
    const { GoConnection, EndConnection } = SnapshotConnection()
    const { business: { info, methods } } = GoConnection;
    
    const Confirm = () => {
        const { street, number } = usePedido;

        if(useStage === 3 && street && number) {
            ReactDOM.render(<ComponentModalComanda { ...{ usePedido, setPedido, info, methods }} />, document.body.appendChild(useComponent))
        }
        else {

            const returnError = useDocment => {
                useDocment.setAttribute('style', 'box-shadow: inset 0 0 0px 1px #e82f2f; background: #ffeaea; color: #e82f2f;');

                setTimeout(() => {
                    useDocment.removeAttribute('style');
                }, 400)
            }

            if(!street) {
                const { current: InputStreet } = useStreet;
                returnError(InputStreet)
            }
            
            if(!number) {
                const { current: InputNumber } = useNumber;
                returnError(InputNumber)
            }
        }
    }

    const Input = (event, data) => {
        const { target: { value } } = event;
        const pedido = { ...usePedido}
        pedido[data] = value;
        setPedido(pedido)
    }

    return (
        <div ref={useThree}>
            <Group numbInput={true}>
                <input type="text" ref={useStreet} placeholder="Rua *" onChange={(event) => Input(event, 'street')} autoCapitalize="on" autoCorrect="off" />
                <input type="text" ref={useNumber} placeholder="Nmero *" onChange={(event) => Input(event, 'number')} autoCapitalize="on" autoCorrect="off" />
            </Group>
            <Group>
                <input type="text" placeholder="Complemento" onChange={(event) => Input(event, 'complement')} autoCapitalize="on" autoCorrect="off" />
            </Group>
            <Alert>
                <p>Ser cobrada a taxa de entrega no valor subtotal do pedido.</p>
            </Alert>
            <Bottom>
                <button type="button" onClick={() => setStage(2)}>Voltar atrs</button>
                <button type="button" onClick={() => Confirm()}>Finalizar pedido</button>
            </Bottom>
        </div>
    )
}

export default ComponentStepThree;