import React, { useEffect, useState } from 'react';
import { ItemCart } from '../ContextIngrediente';

import { Additional, Info, Handle } from '../styles/Manage';

function ComponentAdditional({ assoc, i, extra, id, price, amount, setPrice }) {

    // eslint-disable-next-line no-unused-vars
    const { useCart, setCart } = ItemCart()

    // assoc[0] title
    // assoc[1] limite
    // assoc[2] descrio
    // assoc[3] valor

    // console.log(extra[1][i][0]) //#amount

    const [useAnimate, setAnimate] = useState(false)
    // eslint-disable-next-line no-unused-vars
    const [useAmount, setAmount] = useState(extra[1][i][0])

    useEffect(() => {
        const Pedido = { ...useCart}
        
        if(Pedido[id].extra[1][i][0] !== useAmount) {
            Pedido[id].extra[1][i][0] = useAmount;
            setPrice(Number((price * amount) + extra[1].map((val) => val[0] * val[1]).reduce((total, numero) => total + numero, 0)))
            setCart(Pedido)
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useAmount])

    // eslint-disable-next-line no-unused-vars
    const TransitionLimit = () => {
        setAnimate(true)
        setTimeout(() => setAnimate(false), 500);
    }

    const Decrement = () => useAmount > 0 ? setAmount(prevState => --prevState) : TransitionLimit()
    const Increment = () => useAmount < assoc[1] ? setAmount(prevState => ++prevState) : TransitionLimit()

    return (
        <Additional>
            <Info>
                <span>{assoc[0]} por <strong>{Number(assoc[3]).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</strong></span>
            </Info>
            <Handle animeBtn={useAnimate}>
                <button type="button" onClick={() => Decrement()}>-</button>
                <input type="text" disabled="disabled" value={useAmount} />
                <button type="button" onClick={() => Increment()}>+</button>
            </Handle>
        </Additional>
    )
}

export default ComponentAdditional;