import React, { useEffect } from 'react';
import { SnapshotConnection } from '../ContextConnection';
import { SnapshotStage, SnapshotDelivery, SnapshotRadio } from '../ContextConfirm';

import ComponentDistrictSelect from './ComponentDistrictSelect';

import { Group, Select, Bottom } from '../styles/Container';

function ComponentStepSecond({ useSecond }) {

    // eslint-disable-next-line no-unused-vars
    const { useStage, setStage } = SnapshotStage()
    // eslint-disable-next-line no-unused-vars
    const { useRadio, setRadio } = SnapshotRadio()

    // eslint-disable-next-line no-unused-vars
    const { GoConnection, EndConnection } = SnapshotConnection()
    const { business: { delivery } } = GoConnection;

    // eslint-disable-next-line no-unused-vars
    const { useDelivery, setDelivery } = SnapshotDelivery()

    useEffect(() => {
        if(useRadio && !Object.keys(useDelivery).includes(useRadio)) {
            setRadio('')
        }
    })

    const Input = data => setDelivery(Object.fromEntries(Object.entries(delivery).filter((item) => item[1].zone.toLowerCase().indexOf(data.toLowerCase()) !== -1)))

    const Step = data => {
        if(useRadio) {
            setStage(data)
        }
        else {            
            const { current: ComponentContainer } = useSecond;

            ComponentContainer.setAttribute('style', 'animation-duration: 1s; animation-timing-function: ease-in-out; animation-iteration-count: infinite; -webkit-animation-timing-function: ease-in-out; animation-timing-function: ease-in-out; -webkit-animation-name: headShake; animation-name: headShake;');

            setTimeout(() => {
                ComponentContainer.removeAttribute('style');
            }, 400)
        }
    }

    return (
        <div ref={useSecond}>
            <Group typeColumn={true}>
                <input type="text" placeholder="Buscar bairro..." onChange={({ target: { value } }) => Input(value)} />
            </Group>
            <Select listLength={Object.entries(useDelivery).length}>
                <div>
                    {Object.entries(useDelivery).sort((a, b) => (a[1].zone > b[1].zone) ? 1 : ((b[1].zone > a[1].zone) ? -1 : 0)).map(ComponentDistrictSelect)}
                </div>
            </Select>
            <Bottom>
                <button type="button" onClick={() => setStage(1)}>Voltar atrs</button>
                <button type="button" onClick={() => Step(3)}>Proximo passo</button>
            </Bottom>
        </div>
    )
}

export default ComponentStepSecond;