import React, { useEffect, useState, useRef } from 'react';

import ElementProvider from '../ContextIngrediente';

import ComponentManage from './ComponentManage';

import { Modal, Content, Close } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentModalIngrediente({ usePedido, setPedido, info, useCart, setCart }) {

    const useContent = useRef(null)

    const [usePedido1, setPedido1] = useState(usePedido)
    const { detail } = usePedido1;

    const [useDisplay, setDisplay] = useState(-1)

    useEffect(() => {
        setPedido(usePedido1) //# Quando usePedido1 atualizar, seta o valor de usePedido 
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [usePedido1])

    const modalClose = () => {
        $('[role="modal"]').fadeOut(300, function () {
            this.remove();
        });
    }

    $('[role="modal"]').fadeIn(300)

    return (
        <ElementProvider {...{ usePedido1, setPedido1, info, useDisplay, setDisplay, useCart, setCart }}>
            <Modal>
                <Close onClick={() => modalClose()} />
                <Content ref={useContent} padding="1rem 1.6rem" overflowY="overlay">
                    {detail.map((assoc, index) => <ComponentManage key={assoc.id} { ...{ assoc, index }} />)}
                </Content>
            </Modal>
        </ElementProvider>
    )
}

export default ComponentModalIngrediente;