import React, { useEffect, useRef } from 'react';
import { SnapshotSucess } from '../ContextComanda';

import { Sucess, TextSucess } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentModalSucess({ useContent }) {

    const useFirst = useRef(null)
    
    // eslint-disable-next-line no-unused-vars
    const { useSucess, setSucess } = SnapshotSucess()

    useEffect(() => {
        if(useSucess) {
            
            const { current: content } = useContent;
            const { current: sucess } = useFirst;
            // mantenha este comentario para no bugar a altura;
            $(content).height($(sucess).outerHeight(true))

        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [useSucess])

    return (
        <Sucess ref={useFirst}>
            <div>
                <svg xmlns="http://www.w3.org/2000/svg" width="70" height="70" viewBox="0 0 78.369 78.369" fill="#4CAF50"><path d="M78.049,19.015L29.458,67.606c-0.428,0.428-1.121,0.428-1.548,0L0.32,40.015c-0.427-0.426-0.427-1.119,0-1.547l6.704-6.704 c0.428-0.427,1.121-0.427,1.548,0l20.113,20.112l41.113-41.113c0.429-0.427,1.12-0.427,1.548,0l6.703,6.704 C78.477,17.894,78.477,18.586,78.049,19.015z"/></svg>
            </div>
            <TextSucess>
                <span>Pedido registrado!</span>
                <p>Voc est sendo redicionado para o WhatsApp, confirme a mensagem pronta para nosso contato. Desde j muito obrigado.</p>
            </TextSucess>
        </Sucess>
    )
}

export default ComponentModalSucess;