import React from 'react';
import Bracael from '../Bracael';
import { ItemCart } from '../ContextConnection';

import { Image, Info, Price } from '../styles/Cart';

function ConsponentItensCart() {

    // eslint-disable-next-line no-unused-vars
    const { useCart, setCart } = ItemCart();

    return Object.entries(useCart).sort(function (a, b) {
        return (a[1].moment < b[1].moment) ? 1 : ((b[1].moment < a[1].moment) ? -1 : 0);
    }).map(function (item) {

        const FormatImage = props => {
            const { protocol, hostname, pathname } = new URL(props);
            const PermisionUrl = ['i.imgur.com']
    
            return PermisionUrl.includes(hostname) ? `${protocol}//${hostname}${(() => {
                const splitPath = pathname.split('.')
                return `${splitPath[0]}b.${splitPath[1]}`
            })()}` : props;
        }
                
        const ReduceExtra = item[1].extra[1].map(data => Number(data[0] * data[1])).reduce((a, b) => a + b, 0);

        return (
            <div key={item[0]}>
                <Image>
                    <img src={FormatImage(item[1].thumb)} alt={item[1].title} />
                </Image>
                <Info>
                    <Price>
                        {Number((item[1].price * item[1].amount) + ReduceExtra).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}
                        {item[1].amount > 1 ? <small>{item[1].amount}</small> : null}
                        {ReduceExtra || item[1].comment ? <div>{ReduceExtra ? <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="#e47a2e"><path fillRule="evenodd" data-name="Plus" d="M14,10H10v4a2,2,0,0,1-4,0V10H2A2,2,0,1,1,2,6H6V2a2,2,0,1,1,4,0V6h4A2,2,0,0,1,14,10Z"/></svg> : null}{item[1].comment ? <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="#e47a2e"><path fillRule="evenodd" data-name="Comment" d="M14,12H7L3,16V12H2a2,2,0,0,1-2-2V2A2,2,0,0,1,2,0H14a2,2,0,0,1,2,2v8A2,2,0,0,1,14,12ZM3,3H13V5H3V3ZM3,7H13V9H3V7Z"/></svg> : null}</div> : null}
                    </Price>
                    <span>{item[1].title}</span>
                    <span>{item[1].weight !== undefined ? Bracael.unitSize({ ml: item[1].weight, str: true }) : Bracael.unitSize({ gm: item[1].grams, str: true })}</span>
                </Info>
            </div>
        )
    })
}

export default ConsponentItensCart;