import React from 'react';
import { useHistory, useLocation } from 'react-router-dom';

import { Item, Button } from '../styles/Header';

function ComponentMenuItens() {
    const { pathname } = useLocation()
    const history = useHistory();

    const historyPush = data => (pathname !== data) && history.push(data)

    return (
        <React.Fragment>
            <Item className={'item'}>
                <Button active={pathname === '/hamburguer'} onClick={() => historyPush('/hamburguer')} type='button'>Hambrguer</Button>
            </Item>
            <Item className={'item'}>
                <Button active={pathname === '/promocoes'} onClick={() => historyPush('/promocoes')} className='MENUslct' type='button'>Promoes</Button>
            </Item>
            <Item className={'item'}>
                <Button active={pathname === '/combos'} onClick={() => historyPush('/combos')} className='MENUslct' type='button'>Combos</Button>
            </Item>
            <Item className={'item'}>
                <Button active={pathname === '/porcoes'} onClick={() => historyPush('/porcoes')} className='MENUslct' type='button'>Pores</Button>
            </Item>
            <Item className={'item'}>
                <Button active={pathname === '/bebidas'} onClick={() => historyPush('/bebidas')} className='MENUslct' type='button'>Bebidas</Button>
            </Item>
            <Item className={'item'}>
                <Button active={pathname === '/bomboniere'} onClick={() => historyPush('/bomboniere')} className='MENUslct' type='button'>Bomboniere</Button>
            </Item>
        </React.Fragment>
    )
}

export default ComponentMenuItens;